import styled, { css } from 'styled-components/macro';
import CreatableSelect from 'react-select/creatable';

import colors from 'common/themes/colors';

export const classNamePrefix = 'page-selector';

export const Wrapper = styled.div`
  display: flex;
  align-items: center;
  height: 36px;
  color: ${colors.gray900};
  margin: 24px 24px 0;
  font-size: 13px;
  font-weight: 500;
  font-family: 'system-ui', 'Open Sans', 'Apple SD Gothic Neo', 'Noto Sans KR',
    sans-serif;
`;

export const OptionWrapper = styled.div`
  display: flex;
  align-items: center;
  border-radius: 3px;
  height: 32px;
  padding-left: ${({ isLibrary }) => (isLibrary ? '8px' : '30px')};
  background: ${({ isFocused }) =>
    isFocused ? colors.brand500 : colors.white};
  color: ${({ isSelected }) => (isSelected ? colors.brand500 : colors.gray800)};
  color: ${({ isFocused }) => (isFocused ? colors.white : undefined)};
  cursor: pointer;

  span {
    display: block;
    font-size: 13px;
    max-width: 184px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: inherit;
    cursor: pointer;
  }

  svg {
    margin-right: 8px;
  }

  svg.check {
    margin-left: auto;
    color: inherit;
  }

  ${({ isCreateFolderOption }) =>
    isCreateFolderOption &&
    css`
      flex-wrap: wrap;
    `}

  ${({ isFocused }) =>
    !isFocused &&
    css`
      strong {
        color: ${colors.brand500};
      }
    `}
`;

export const InputWrapper = styled.div`
  display: flex;
  align-items: center;
  color: ${colors.gray600};
  cursor: text;

  & > div {
    cursor: text;
  }

  .${classNamePrefix}__input {
    padding-right: 22px;
    font-size: 13px;
    color: ${colors.gray600};
    cursor: text;

    input {
      cursor: text;
      &::placeholder {
        color: ${colors.gray500};
      }
    }
  }

  svg {
    margin-right: 1px;
    margin-left: -3px;
  }
`;

export const StyledSelect = styled(CreatableSelect)`
  width: 100%;
  color: ${colors.gray900};

  .${classNamePrefix}__control {
    display: inline-flex;
    width: 100%;
    padding: 0 8px;
    background: transparent;
    border: none;
    cursor: pointer;
    min-height: 36px;
    box-shadow: none;
    transition: none;
    background: ${colors.gray50};

    &:hover {
      background: ${colors.gray80};
    }

    &--menu-is-open {
      background: ${colors.gray50};
      cursor: default;

      .${classNamePrefix}__placeholder {
        display: none;
        color: ${colors.gray600};
        svg {
          background: transparent;
        }
      }

      .${classNamePrefix}__single-value {
        display: none;
      }

      .${classNamePrefix}__indicators {
        display: none;
        opacity: 1;
      }
    }
  }

  .${classNamePrefix}__value-container {
    padding: 0;
    cursor: pointer;
  }

  .${classNamePrefix}__single-value {
    display: flex;
    width: 100%;
    align-items: center;
    font-size: 13px;
    margin: 0;
    max-width: initial;
    cursor: pointer;

    svg.icon {
      color: ${colors.gray700};
      margin-right: 8px;
    }

    svg.tobottom {
      color: ${colors.gray400};
      margin-left: auto;
    }

    span {
      display: block;
      max-width: 180px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      color: ${colors.gray800};
      cursor: pointer;
    }
  }

  .${classNamePrefix}__placeholder {
    display: flex;
    position: relative;
    padding-right: 36px;
    margin: 0;
    top: initial;
    transform: initial;
    align-items: center;
    font-size: 13px;
    color: ${colors.gray500};

    svg {
      margin-right: 8px;
      border-radius: 50%;
      background: ${colors.gray80};
    }
  }

  .${classNamePrefix}__menu {
    display: block;
    width: 100%;
    margin: 0;
    padding: 8px;
    border-radius: 3px;
    box-shadow: 0 2px 24px -4px rgba(0, 0, 0, 0.1);
    left: 0;
  }

  .${classNamePrefix}__menu-list {
    display: block;
    max-height: 320px;
    padding: 0;
  }
`;

export const FolderInputWrapper = styled.div`
  display: flex;
  align-items: center;
  padding-left: 30px;
  border-radius: 3px;
  background-color: ${colors.gray80};

  svg {
    margin-right: 10px;
  }
`;

export const FolderInput = styled.input`
  width: 100%;
  height: 32px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: none;
  outline: none;
  color: ${colors.gray900};
  background: ${colors.transparent};

  &::placeholder {
    color: ${colors.gray400};
  }
`;
