import React, { useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import styled from 'styled-components/macro';
import { connect } from 'react-redux';
import { setSaving, setSaved } from 'contentScript/redux/action';
import { sendMessageToChangeCommentMessage } from 'contentScript/messagePassing/sendMessage';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';

const CommentInput = styled.textarea`
  ${helpers.forceInitialize}
  resize: none !important;
  height: 72px !important;
  width: calc(100% - 33px - 33px) !important;
  font-size: 13px !important;
  line-height: 1.85 !important;
  outline: none !important;
  color: ${colors.gray700} !important;
  margin: 10px 33px 8px 33px !important;
  padding: 0 !important;
  border: none !important;
  &:focus {
    outline: none !important;
  }
  &::placeholder {
    color: ${colors.gray400} !important;
  }
`;

const updateComment = _.debounce(async (commentMessage, dispatchSetSaved) => {
  await sendMessageToChangeCommentMessage(commentMessage);
  dispatchSetSaved();
}, 1000);

function Comment({ setSaving, setSaved, blockUserAction, currentWorkspaceId }) {
  const [comment, setComment] = React.useState('');
  const [prevWorkspaceId, setPrevWorkspaceId] = React.useState('');
  const handleChange = useCallback(e => {
    const targetText = e.target.value;
    setSaving();
    updateComment(targetText, setSaved);
    setComment(targetText);
  }, []);

  useEffect(() => {
    if (prevWorkspaceId === '') {
      setPrevWorkspaceId(currentWorkspaceId);
    } else if (currentWorkspaceId !== prevWorkspaceId) {
      setSaving();
      updateComment(comment, setSaved);
      setPrevWorkspaceId(currentWorkspaceId);
    }
  }, [currentWorkspaceId]);

  return (
    <CommentInput
      value={comment}
      onChange={handleChange}
      spellcheck={false}
      placeholder="Save with comment"
      disabled={blockUserAction}
    />
  );
}

Comment.propTypes = {
  setSaving: PropTypes.func.isRequired,
  setSaved: PropTypes.func.isRequired,
  blockUserAction: PropTypes.bool.isRequired,
  currentWorkspaceId: PropTypes.string.isRequired,
};

const mapStateToProps = state => {
  const {
    widget: { currentWorkspaceId, blockUserAction },
  } = state;
  return {
    currentWorkspaceId,
    blockUserAction,
  };
};

const mapDispatchToProps = {
  setSaving,
  setSaved,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Comment);
