import { Folder } from '@additor/elements';
import React, {
  useCallback,
  useState,
  useEffect,
  useMemo,
  useRef,
} from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { connect } from 'react-redux';
import { components as ReactSelectComponents } from 'react-select';

import { moveCard, createFolder } from 'contentScript/redux/action';
import { setSaving, setSaved } from 'contentScript/redux/action';
import HighlightContent from './HighlightContent';

import SvgLibrary from '../icons/library.svg';
import SvgSearch from '../icons/search.svg';
import SvgBottom from '../icons/tobottom.svg';
import SvgCheck from '../icons/select-check.svg';
import SvgFolder from '../icons/folder.svg';
import SvgPlus from '../icons/folder-plus.svg';

import {
  Wrapper,
  StyledSelect,
  classNamePrefix,
  OptionWrapper,
  InputWrapper,
  FolderInputWrapper,
  FolderInput,
} from './styled';

const LIBRARY_OPTION = {
  value: 'LIBRARY',
  label: 'Library',
};

const CREATE_FOLDER_OPTION = {
  value: 'CREATE_FOLDER',
  label: 'New folder',
};

function validateFolderName(name) {
  return !_.isEmpty(name);
}

function CreateFolderOption(props) {
  const {
    children,
    isFocused,
    innerRef,
    innerProps,
    selectProps,
    data,
  } = props;

  const handleClick = useCallback(
    e => {
      if (data.value === CREATE_FOLDER_OPTION.value) {
        selectProps.setFolderInputVisible(true);
        e.preventDefault();
        e.stopPropagation();
      } else {
        innerProps.onClick(e);
      }
    },
    [data],
  );

  const handleKeyDown = useCallback(e => {
    switch (e.keyCode) {
      case 13:
      case 9:
        {
          const folderName = _.trim(e.target.value);
          if (validateFolderName(folderName)) {
            selectProps.createFolder({ name: folderName });
            selectProps.setFolderInputVisible(false);
            selectProps.focus();
          }
          e.preventDefault();
          e.stopPropagation();
        }
        break;
      case 27:
        selectProps.setFolderInputVisible(false);
        selectProps.focus();
        e.preventDefault();
        e.stopPropagation();
        break;
      default:
        break;
    }
    e.stopPropagation(); // react-select 의 searchInput 이 포커스를 뺏어가지 못하도록 처리
  }, []);

  const handleBlur = useCallback(() => {
    selectProps.setFolderInputVisible(false);
  }, []);

  const handleMouseDownOnInput = useCallback(e => {
    // 이벤트가 전파되어 팝업이 닫히는 버그 해결을 위함
    e.stopPropagation();
  }, []);

  const handleClickOnInput = useCallback(e => {
    // 이벤트가 전파되어 팝업이 닫히는 버그 해결을 위함
    e.stopPropagation();
  }, []);

  return (
    <>
      <OptionWrapper
        ref={innerRef}
        {...innerProps}
        isCreateFolderOption
        isFocused={isFocused}
        onClick={handleClick}
      >
        <SvgPlus />
        <span>{children}</span>
      </OptionWrapper>
      {selectProps.folderInput && (
        <FolderInputWrapper
          onMouseDown={handleMouseDownOnInput}
          onClick={handleClickOnInput}
        >
          <SvgFolder />
          <FolderInput
            autoFocus
            maxLength={Folder.MAX_NAME_LENGTH}
            placeholder="Please name the new folder"
            onKeyDown={handleKeyDown}
            onBlur={handleBlur}
          />
        </FolderInputWrapper>
      )}
    </>
  );
}

function Option(props) {
  const {
    children,
    isFocused,
    isSelected,
    innerRef,
    innerProps,
    selectProps,
    data,
  } = props;

  if (data.value === CREATE_FOLDER_OPTION.value) {
    return <CreateFolderOption {...props} />;
  }

  const isLibrary = data.value === LIBRARY_OPTION.value;
  const isCreateOptionWhileSearching = Boolean(data.__isNew__);

  return (
    <OptionWrapper
      ref={innerRef}
      {...innerProps}
      isSelected={isSelected}
      isFocused={isFocused}
      isLibrary={isLibrary}
    >
      {isLibrary ? (
        <SvgLibrary />
      ) : isCreateOptionWhileSearching ? (
        <SvgPlus />
      ) : (
        <SvgFolder />
      )}
      <span>
        {isCreateOptionWhileSearching ? (
          children
        ) : (
          <HighlightContent keyword={selectProps.inputValue}>
            {children}
          </HighlightContent>
        )}
      </span>
      {isSelected && <SvgCheck className="check" />}
    </OptionWrapper>
  );
}

function SingleValue({ children, data, ...props }) {
  return (
    <ReactSelectComponents.SingleValue {...props}>
      <>
        {data.value === LIBRARY_OPTION.value ? (
          <SvgLibrary className="icon" />
        ) : (
          <SvgFolder className="icon" />
        )}
        <span>{children}</span>
        <SvgBottom className="tobottom" />
      </>
    </ReactSelectComponents.SingleValue>
  );
}

function Input(props) {
  const { selectProps } = props;
  if (!selectProps.menuIsOpen) {
    return <ReactSelectComponents.Input {...props} />;
  }
  return (
    <InputWrapper theme={selectProps.theme}>
      <SvgSearch />
      <ReactSelectComponents.Input
        {...props}
        placeholder="Search for a folder"
      />
    </InputWrapper>
  );
}

function Menu(props) {
  return (
    <ReactSelectComponents.Menu {...props}>
      {props.children}
    </ReactSelectComponents.Menu>
  );
}

function customFilter(option, searchText) {
  const { value, label } = option;
  if (!_.isEmpty(searchText)) {
    const lowerSearchText = _.toLower(searchText);
    return (
      value !== LIBRARY_OPTION.value &&
      value !== CREATE_FOLDER_OPTION.value &&
      _.includes(_.toLower(label), lowerSearchText)
    );
  }
  return true;
}

function FolderSelector({
  cardId,
  folders,
  recentFolderId,
  isSaving,
  moveCard,
  createFolder,
  setSaving,
  setSaved,
}) {
  const selectorRef = useRef();
  const recentFolder = useMemo(
    () => _.find(folders, ({ id }) => id === recentFolderId),
    [folders, recentFolderId],
  );
  const folderOptions = useMemo(
    () =>
      _.map(folders, ({ id, name }) => ({
        value: id,
        label: name,
      })),
    [folders],
  );
  const [folderInput, setFolderInputVisible] = useState(false);
  const [menuIsOpen, setMenuIsOpen] = useState(false);
  const [currentOption, setOption] = useState(
    recentFolder
      ? { value: recentFolder.id, label: recentFolder.name }
      : LIBRARY_OPTION,
  );
  const [isLoading, setLoading] = useState(false);

  const prevRecentFolderId = useRef();
  useEffect(() => {
    if (prevRecentFolderId.current !== recentFolderId) {
      const folder = _.find(folders, ({ id }) => id === recentFolderId);
      setOption(
        folder
          ? {
              value: folder.id,
              label: folder.name,
            }
          : LIBRARY_OPTION,
      );
    }
    prevRecentFolderId.current = recentFolderId;
  }, [folders, recentFolderId]);

  useEffect(() => {
    // isSaving 이 false 가 되었을 때, menu 가 열리는 버그가 있어서 강제로 menu 가 닫히고, 인풋이 blur 되도록 처리
    if (!isSaving) {
      setMenuIsOpen(false);
      selectorRef.current.blur();
    }
  }, [isSaving]);

  const focus = () => {
    selectorRef.current.focus();
  };

  const handleFocusSelect = useCallback(e => {
    e.preventDefault();
    setMenuIsOpen(true);
  }, []);

  const handleBlurSelect = useCallback(() => {
    setMenuIsOpen(false);
  }, []);

  const handleChange = useCallback(
    option => {
      if (currentOption.value === option.value) {
        setMenuIsOpen(false);
      } else if (option.value === LIBRARY_OPTION.value) {
        (async () => {
          setLoading(true);
          await moveCard({ cardId });
          setLoading(false);
        })();
        setOption(LIBRARY_OPTION);
        setMenuIsOpen(false);
      } else if (option.value === CREATE_FOLDER_OPTION.value) {
        setFolderInputVisible(true);
      } else {
        (async () => {
          setLoading(true);
          await moveCard({ cardId, parent: option.value });
          setLoading(false);
        })();
        setOption(option);
        setMenuIsOpen(false);
      }
    },
    [cardId, currentOption],
  );

  const handleCreateOption = useCallback(label => {
    (async () => {
      setLoading(true);
      setSaving();
      const response = await createFolder({
        name: label,
        shouldMoveCard: true,
      });
      const { createdFolder } = response.data;
      setOption({
        value: createdFolder.id,
        label: createdFolder.name,
      });
      setLoading(false);
      setSaved();
    })();
    setMenuIsOpen(false);
  }, []);

  const handleKeyDown = useCallback(e => {
    switch (e.key) {
      case 'Escape':
        setMenuIsOpen(false);
        break;
      default:
        break;
    }
  }, []);

  return (
    <Wrapper onClick={focus}>
      <StyledSelect
        ref={selectorRef}
        value={currentOption}
        onChange={handleChange}
        isLoading={isLoading}
        onKeyDown={handleKeyDown}
        onFocus={handleFocusSelect}
        onBlur={handleBlurSelect}
        onCreateOption={handleCreateOption}
        menuIsOpen={menuIsOpen || folderInput}
        folderInput={folderInput}
        setFolderInputVisible={setFolderInputVisible}
        setMenuIsOpen={setMenuIsOpen}
        createOptionPosition="first"
        options={_.concat(
          [LIBRARY_OPTION, CREATE_FOLDER_OPTION],
          folderOptions,
        )}
        isDisabled={isSaving}
        classNamePrefix={classNamePrefix}
        backspaceRemovesValue={false}
        filterOption={customFilter}
        placeholder={
          <>
            <SvgLibrary />
            <span>Library</span>
          </>
        }
        noOptionsMessage={() => 'No member matched'}
        createFolder={createFolder}
        focus={focus}
        components={{
          Option,
          SingleValue,
          Input,
          Menu,
          ClearIndicator: () => null,
          IndicatorSeparator: () => null,
          DropdownIndicator: () => null,
          LoadingIndicator: () => null,
        }}
      />
    </Wrapper>
  );
}

FolderSelector.propTypes = {
  cardId: PropTypes.string,
  folders: PropTypes.array,
  recentFolderId: PropTypes.string,
  isSaving: PropTypes.bool,
  moveCard: PropTypes.func,
  createFolder: PropTypes.func,
  setSaving: PropTypes.func,
  setSaved: PropTypes.func,
};

export default connect(
  ({ widget, card }) => ({
    cardId: card.cardId,
    folders: widget.folders,
    recentFolderId: widget.recentFolderId,
    isSaving: widget.isSaving || widget.blockUserAction,
  }),
  dispatch => ({
    moveCard: moveCard(dispatch),
    createFolder: createFolder(dispatch),
    setSaving: () => dispatch(setSaving()),
    setSaved: () => dispatch(setSaved()),
  }),
)(FolderSelector);
