import _ from 'lodash';
import classNames from 'classnames';
import { getNodeByPath } from './xPath';
import { getTextNodesInRange, markNode, unmarkNode } from './DOM';
import {
  HIGHLIGHT_COLORS,
  getAllHighlightIds,
  getColorName,
  updateLocalHighlightColor,
  deleteLocalHighlight,
} from './highlightStore';
import { removeHighlightScrollPoint } from './highlightScrollPoint';

function getHighlightBaseNodePath(highlight) {
  const separatorsIndex = [];

  const { start: startPath, end: endPath } = highlight;
  for (let i = 0; i < startPath.length; i += 1) {
    if (startPath[i] === '/') separatorsIndex.unshift(i);
  }

  for (let i = 0; i < separatorsIndex.length; i += 1) {
    if (endPath.startsWith(startPath.slice(0, separatorsIndex[i]))) {
      return startPath.slice(0, separatorsIndex[i]);
    }
  }
  return '';
}

export function normalizeHighlightRange(highlight) {
  if (highlight.start === highlight.end) {
    return {
      normalizedRange: [getNodeByPath(highlight.start)],
      startOffset: highlight.startOffset,
      endOffset: highlight.endOffset,
    };
  }
  const startNode = getNodeByPath(highlight.start);
  const endNode = getNodeByPath(highlight.end);
  const baseNode = getNodeByPath(getHighlightBaseNodePath(highlight));
  const normalizedRange = getTextNodesInRange(baseNode, startNode, endNode);
  return {
    normalizedRange,
    startOffset: highlight.startOffset,
    endOffset: highlight.endOffset,
  };
}

export function markHighlightText({ highlight, onClick }) {
  const highlightId = highlight.id;
  let highlightRange;
  try {
    highlightRange = normalizeHighlightRange(highlight);
  } catch (e) {
    highlightRange = null;
  }
  if (!highlightRange) return;
  const { normalizedRange, startOffset, endOffset } = highlightRange;
  const className = classNames('additor-highlight', {
    [`additor-highlight--${highlightId}`]: !!highlightId,
  });
  for (let i = 0; i < normalizedRange.length; i += 1) {
    markNode(normalizedRange[i], {
      startOffset: i === 0 ? startOffset : undefined,
      endOffset: i === normalizedRange.length - 1 ? endOffset : undefined,
      className,
      onClick,
      attrs: {
        'data-highlight-id': highlightId,
        'data-color': getColorName(highlight.color),
      },
    });
  }
}

export function changeHighlightColor(highlightId, colorName) {
  _.forEach(
    document.getElementsByClassName(`additor-highlight--${highlightId}`),
    e => {
      e.setAttribute('data-color', colorName);
    },
  );
  updateLocalHighlightColor(highlightId, HIGHLIGHT_COLORS[colorName]);
}

export function removeHighlight(highlightId) {
  const marks = document.getElementsByClassName(
    `additor-highlight--${highlightId}`,
  );
  unmarkNode(marks);
  deleteLocalHighlight(highlightId);
}

export function removeAllHighlights() {
  const highlightIds = getAllHighlightIds();
  _.forEach(highlightIds, id => {
    removeHighlight(id);
    removeHighlightScrollPoint(id);
  });
}
