import _ from 'lodash';
import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import OutsideClickHandler from 'react-outside-click-handler';
import styled from 'styled-components/macro';
import getHostName from 'common/getHostName';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';
import SvgCheck from '../icons/check.svg';
import SvgPlus from '../icons/plus.svg';
import {
  WorkspaceTextThumbnail,
  WorkspaceImageThumbnail,
} from './WorkspaceThumbnails';

export const MenuPopup = styled.div`
  width: 180px;
  left: 23px;
  margin-top: 17px;
  padding: 8px 8px 0 8px;
  border-radius: 3px;
  box-shadow: 0 2px 24px -4px rgba(0, 0, 0, 0.1);
  background-color: ${colors.white};
  position: absolute;
  z-index: 1;
`;

const MenuItem = styled.div`
  display: flex;
  align-items: center;
  padding: 8px;
  width: 164px;
  height: 32px;
  border-radius: 3px;
  font-size: 13px;
  background-color: ${colors.transparent};
  color: ${colors.gray900};
  cursor: pointer;
  ${helpers.transition('color', 'background-color')}
  span {
    width: 120px;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    white-space: nowrap;
    line-height: 18px;
  }
  svg {
    ${helpers.transition('color')}
    color: ${colors.brand500};
  }
  
  &:hover {
    span, svg {
      color: ${colors.white};
    }
    background-color: ${colors.brand500};
  }
`;

const AddWorkspaceButton = styled(MenuItem)`
  border-top: 1px solid ${colors.gray100};
  height: 34px;
  svg {
    margin-right: 8px;
    path {
      fill: ${colors.brand500};
    }
  }
  &:hover {
    color: ${colors.brand500};
    background-color: ${colors.transparent};
  }
`;

const WorkspaceMenuItem = ({
  id,
  name,
  thumbnail,
  ctime,
  onClick,
  checked = false,
}) => {
  const handleClickMenuItem = useCallback(() => {
    onClick(id);
  }, [id, onClick]);

  return (
    <MenuItem onClick={handleClickMenuItem}>
      {thumbnail === '' ? (
        <WorkspaceTextThumbnail ctime={ctime} size={16}>
          {_.toUpper(_.head(name))}
        </WorkspaceTextThumbnail>
      ) : (
        <WorkspaceImageThumbnail src={thumbnail} alt={name} size={16} />
      )}
      <span title={name}>{name}</span>
      {checked && <SvgCheck />}
    </MenuItem>
  );
};

WorkspaceMenuItem.propTypes = {
  id: PropTypes.string.isRequired,
  name: PropTypes.string.isRequired,
  thumbnail: PropTypes.string.isRequired,
  ctime: PropTypes.number.isRequired,
  onClick: PropTypes.func.isRequired,
  checked: PropTypes.bool.isRequired,
};

function WorkspaceMenuPopup({
  workspaceList,
  currentWorkspaceId,
  onClickMenuItem,
  onClickOutside,
}) {
  const handleClickAddWorkspaceButton = useCallback(() => {
    window.open(`https://${getHostName()}/sign-in`);
  }, []);
  return (
    <OutsideClickHandler onOutsideClick={onClickOutside}>
      <MenuPopup>
        {_.map(workspaceList, ({ name, id, ctime, thumbnail }) => (
          <WorkspaceMenuItem
            key={id}
            id={id}
            name={name}
            ctime={ctime}
            thumbnail={thumbnail || ''}
            checked={currentWorkspaceId === id}
            onClick={onClickMenuItem}
          />
        ))}
        <AddWorkspaceButton onClick={handleClickAddWorkspaceButton}>
          <SvgPlus />
          Add another
        </AddWorkspaceButton>
      </MenuPopup>
    </OutsideClickHandler>
  );
}

WorkspaceMenuPopup.propTypes = {
  workspaceList: PropTypes.array.isRequired,
  currentWorkspaceId: PropTypes.string.isRequired,
  onClickMenuItem: PropTypes.func.isRequired,
  onClickOutside: PropTypes.func.isRequired,
};

export default WorkspaceMenuPopup;
