import _ from 'lodash';
import { Highlight } from '@additor/elements';
import { createXPathTo } from './xPath';

export const HIGHLIGHT_COLORS = {
  purple: '#7552f6',
  blue: '#4a90e2',
  green: '#50e3c2',
  yellow: '#f8e71c',
  red: '#f74e63',
};

let _lastSelectedColorName = HIGHLIGHT_COLORS.purple;
let _highlights = {};

export function getLastSelectedColorName() {
  return _lastSelectedColorName;
}

export function getLastSelectedColorCode() {
  return HIGHLIGHT_COLORS[_lastSelectedColorName];
}

export function setLastSelectedColorName(colorName) {
  _lastSelectedColorName = colorName;
}

export function getColorName(colorCode) {
  return Object.keys(HIGHLIGHT_COLORS).find(
    n => HIGHLIGHT_COLORS[n] === colorCode,
  );
}

export function getColorCode(colorName) {
  return HIGHLIGHT_COLORS[colorName];
}

export function getHighlightContents(id) {
  const highlight = _highlights[id];
  return highlight.content;
}

export function setLocalHighlight(highlight) {
  _highlights[highlight.id] = highlight;
}

export function getLocalHighlight(highlightId) {
  return _highlights[highlightId];
}

export function getAllHighlightIds() {
  return _.keys(_highlights);
}

export function updateLocalHighlightColor(highlightId, color) {
  _highlights[highlightId].color = color;
}

export function deleteLocalHighlight(highlightId) {
  delete _highlights[highlightId];
}

export function hasHighlight() {
  return !_.isEmpty(_highlights);
}

export function createHighlightWithSelection(textSelection, color) {
  if (!textSelection || _.trim(textSelection.toString()) === '') {
    return null;
  }

  const {
    startContainer,
    startOffset,
    endContainer,
    endOffset,
  } = textSelection.getRangeAt(0);

  const highlight = new Highlight();

  highlight.setColor(color).setContent(textSelection.toString());

  if (startContainer === endContainer) {
    highlight
      .setStart(
        createXPathTo(startContainer),
        startOffset,
        textSelection.toString(),
      )
      .setEnd(createXPathTo(endContainer), endOffset, textSelection.toString());
  } else {
    const startData = startContainer.data
      ? startContainer.data.slice(startOffset)
      : '';
    const endContents = endContainer.data
      ? endContainer.data.slice(0, endOffset)
      : '';
    highlight
      .setStart(createXPathTo(startContainer), startOffset, startData)
      .setEnd(createXPathTo(endContainer), endOffset, endContents);
  }
  return highlight;
}
