import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { baseElementId } from './appendBaseElementToDocument';
import {
  sendMessageToGetInitialStates,
  sendMessageToOpenSignInPage,
} from './messagePassing/sendMessage';
import { getStore, createStore, removeStore } from './redux/store';
import { showWidget } from './redux/action';
import Widget from './views/Widget';
import { removeAllHighlights } from './highlighter/highlight';
import { removeTooltip } from './highlighter/annotationTooltip';
import extractBaseCardData from './extractBaseCardData';
import ErrorBoundary from './views/ErrorBoundary';
import ErrorView from './views/ErrorBoundary/ErrorView';

function isURIChanged(store, currentURI) {
  // Youtube 와 같은 SPA site 인 경우 widget 이 열릴 때,
  // 이전 애딧과 uri 가 다르면 현재 애딧을 없애고 새로 애딧을 생성한다.
  const globalState = store.getState();
  const { cardUri } = globalState.card;
  return cardUri !== currentURI;
}

export async function openWidget() {
  const metaData = await extractBaseCardData(); // uri, favicon, thumbnails 만 사용함
  let store = getStore();
  if (store) {
    if (isURIChanged(store, metaData.uri)) {
      unmountWidget();
      openWidget();
    } else {
      store.dispatch(showWidget());
    }
  } else {
    const initialStates = await sendMessageToGetInitialStates(
      metaData.uri,
      metaData.thumbnails,
    );
    if (initialStates === null) {
      // 401 (maybe token expired)
      // TODO: token expired 일때 문구변경
      ReactDOM.render(
        <ErrorView
          description={
            'Oops! You can’t access this workspace.\n' +
            'You might have been removed from it or your token has been expired.\n' +
            'Please sign in to your workspace from the website.'
          }
          buttonText="Sign in"
          onClick={() => {
            sendMessageToOpenSignInPage();
            unmountWidget();
          }}
        />,
        document.getElementById(baseElementId),
      );
    } else {
      store = createStore(initialStates);
      ReactDOM.render(
        <Provider store={store}>
          <ErrorBoundary>
            <Widget />
          </ErrorBoundary>
        </Provider>,
        document.getElementById(baseElementId),
      );
    }
  }
}

export function unmountWidget() {
  removeAllHighlights();
  removeTooltip();
  removeStore();
  ReactDOM.unmountComponentAtNode(document.getElementById(baseElementId));
}
