import React, { useEffect, useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import * as Sentry from '@sentry/browser';
import { Authority } from '@additor/elements';
import styled from 'styled-components/macro';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';
import { hideWidget, createCard } from 'contentScript/redux/action';

import ErrorView from '../ErrorBoundary/ErrorView';
import Header from './Header';
import Card from './Card';
import FolderSelector from './FolderSelector';
import Comment from './Comment';
import WorkspaceSelector from './WorkspaceSelector';
import Footer from './Footer';
import SvgDisable from './icons/disable.svg';
import { sendMessageToDeleteWorkspace } from 'contentScript/messagePassing/sendMessage';
import Tags from './Tags';

const WidgetWrapper = styled.div`
  display: ${({ isHidden }) => (isHidden ? 'none' : 'block')};
  width: 335px;
  min-height: 320px;
  max-height: 500px;
  position: fixed;
  top: 10px;
  right: 10px;
  background: #fff;
  z-index: 2147483631;
  border-radius: 5px;
  box-shadow: 0 4px 24px -4px rgba(0, 0, 0, 0.18);
  font-size: 1rem;
  color: ${colors.gray900};
  font-family: 'system-ui', 'Open Sans', 'Apple SD Gothic Neo', 'Noto Sans KR',
    sans-serif;
`;

const UserActionBlockerWrapper = styled.div`
  ${helpers.centerFlex}
  flex-direction: column;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 92px;
  background-color: ${colors.gray100};
  opacity: 0.95;
  border-radius: 5px;
  z-index: 1;

  span {
    ${helpers.centerFlex}
    position: absolute;
    top: 16px;
    right: 24px;
    color: ${colors.red400};
    font-size: 13px;
    font-weight: 500;
  }

  svg {
    color: ${colors.red400};
    margin-left: 6px;
  }

  h1 {
    margin: 0 0 20px 0;
    padding: 0;
    font-size: 18px;
    font-weight: 600;
    color: ${colors.gray900};
  }

  p {
    margin: 0 0 2px 0;
    padding: 0;
    font-size: 14px;
    color: ${colors.gray900};
  }

  button {
    ${helpers.forceInitialize}
    ${helpers.forceCenterFlex}
    margin-top: 25px !important;
    width: 67px !important;
    height: 30px !important;
    font-size: 13px !important;
    font-weight: 500 !important;
    color: ${colors.white} !important;
    background-color: ${colors.brand500} !important;
    cursor: pointer !important;
    border-radius: 3px !important;
    &:hover {
      background-color: ${colors.brand700} !important;
    }
    &:active {
      background-color: ${colors.brand800} !important;
    }
  }
`;

function shouldWidgetHideOnClick(event) {
  return (
    !event.target.closest('#additor-crx-base') &&
    !event.target.closest('.additor-settings-root')
  );
}

const Widget = ({
  isHidden,
  isGuest,
  hideWidget: dispatchHideWidget,
  disableWorkspace = false,
  signedOutWorkspace = false,
  cardUri,
  recentFolderId,
  createCard,
  user,
  error = false,
}) => {
  const [isPdf, setIsPdf] = useState(false);
  useEffect(() => {
    if (isGuest) {
      return;
    }

    Sentry.init({
      dsn: process.env.SENTRY_DSN,
      maxBreadcrumbs: 50,
    });

    Sentry.configureScope(scope => {
      scope.setUser({
        id: user?.id,
        username: user?.name,
        email: user?.email,
      });
    });

    document.addEventListener('mousedown', e => {
      if (shouldWidgetHideOnClick(e)) {
        dispatchHideWidget();
      }
    });
    if (document.querySelector('body > embed[type="application/pdf"]')) {
      setIsPdf(true);
      document.addEventListener('keydown', e => {
        if (e.key === 'Escape') {
          if (e.target.tagName === 'textarea' || e.target.tagName === 'input') {
            return;
          }
          dispatchHideWidget();
        }
      });
    }
    createCard({ cardUri, parent: recentFolderId });
  }, []);

  const handleClickOkayButton = useCallback(async () => {
    sendMessageToDeleteWorkspace();
  }, []);

  if (error) {
    return <ErrorView />;
  }

  return (
    <WidgetWrapper isHidden={isHidden}>
      {signedOutWorkspace && (
        <UserActionBlockerWrapper>
          <span>
            Unaccessible
            <SvgDisable />
          </span>
          <h1>Oops!</h1>
          <p>Your token expired - already signed out</p>
        </UserActionBlockerWrapper>
      )}
      {!signedOutWorkspace && disableWorkspace && (
        <UserActionBlockerWrapper>
          <span>
            Unaccessible
            <SvgDisable />
          </span>
          <h1>Oops!</h1>
          <p>You can’t access this workspace any more.</p>
          <p>Its name will be removed from the list, too.</p>
          <button onClick={handleClickOkayButton}>Okay</button>
        </UserActionBlockerWrapper>
      )}
      {isGuest && (
        <UserActionBlockerWrapper>
          <span>
            No permission
            <SvgDisable />
          </span>
          <h1>Oops!</h1>
          <p>You’re a guest of this workspace.</p>
          <p>Guest can’t access the library so you</p>
          <p>can’t save cards in this workspace.</p>
        </UserActionBlockerWrapper>
      )}
      <Header
        onClickCloseWidgetButton={dispatchHideWidget}
        showCloseButton={isPdf}
      />
      <Card />
      <FolderSelector />
      <Comment />
      <Tags />
      <WorkspaceSelector />
      <Footer />
    </WidgetWrapper>
  );
};

Widget.propTypes = {
  isHidden: PropTypes.bool.isRequired,
  isGuest: PropTypes.bool.isRequired,
  disableWorkspace: PropTypes.bool,
  signedOutWorkspace: PropTypes.bool,
  cardUri: PropTypes.string.isRequired,
  recentFolderId: PropTypes.string.isRequired,
  hideWidget: PropTypes.func.isRequired,
  createCard: PropTypes.func.isRequired,
  user: PropTypes.object.isRequired,
  error: PropTypes.bool, // true if received error from sendMessageTo<something>'s response
};

const mapStateToProps = state => {
  const {
    widget: {
      isHidden,
      myRole,
      disableWorkspace = false,
      signedOutWorkspace = false,
      error,
      recentFolderId,
      user,
    },
    card: { cardUri },
  } = state;
  return {
    isHidden: isHidden,
    isGuest: myRole === Authority.EntireRoles['GUEST'],
    disableWorkspace,
    signedOutWorkspace,
    cardUri,
    recentFolderId,
    error,
    user,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    hideWidget: () => dispatch(hideWidget()),
    createCard: createCard(dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Widget);
