/**
 * delegate to background
 * 1. create card
 * 2. delete card
 * 3. create comment
 * 4. update comment
 * 5. delete comment
 * 4. update card title
 * 5. update card thumb
 * 6. update card tags
 * 7.
 */

export function sendMessageToGetMockStates() {
  return new Promise(resolve => {
    resolve(mockData);
  });
}

function handleError(e) {
  console.error('error', e);
  handleShowErrorView();
}

export function sendMessageToGetInitialStates(uri, thumbnails) {
  return new Promise(resolve => {
    chrome.runtime.sendMessage(
      generateRequest({
        type: MessageTypes.GET_INITIAL_STATES,
      }),
      null,
      response => {
        if (response.ok) {
          const { initialStates } = response.data;
          resolve({
            widget: {
              disableWorkspace: false,
              signedOutWorkspace: false,
              error: false,
              ...initialStates.widget,
              savedHighlightLength: 0,
              isSaving: true,
              isSaved: false,
              isUndoing: false,
              isUndone: false,
              isHidden: false,
              searchInputText: '',
              blockUserAction: false,
            },
            card: {
              cardId: '',
              cardEid: '',
              cardTitle: '',
              cardDescription: '',
              cardTags: [],
              cardUri: uri,
              cardThumbnails: thumbnails,
            },
          });
        } else {
          const store = getStore();
          if (!store) {
            resolve(null);
          }
          handleError(response.error);
        }
      },
    );
  });
}

export function sendMessageToCreateCard({ cardUri, parent = null }) {
  return new Promise(resolve => {
    chrome.runtime.sendMessage(
      generateRequest({
        type: MessageTypes.CREATE_CARD,
        data: {
          cardUri,
          parent,
        },
      }),
      null,
      response => {
        if (response.ok) {
          resolve(response);
        } else {
          handleError(response.error);
        }
      },
    );
  });
}

