import { Authority } from '@additor/elements';
import React, { useCallback, useState } from 'react';
import _ from 'lodash';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import styled from 'styled-components/macro';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';
import { cardDefaultThumbSrc } from 'common/constants';
import SvgToRight from './icons/toright.svg';
import { updateCardThumbnail, updateCardTitle } from '../../redux/action';

