import _ from 'lodash';
import classNames from 'classnames';

import { baseElementId } from '../appendBaseElementToDocument';
import {
  HIGHLIGHT_COLORS,
  getColorName,
  setLastSelectedColorName,
} from './highlightStore';
import { calculateTextRangeRects } from './selection';

function getAnnotationRoot() {
  return document.getElementById(baseElementId);
}

function getTooltipPosition(range) {
  const rect = {
    top: 0,
    left: 0,
  };
  const textRects = calculateTextRangeRects(range);
  const lastTextRect = _.last(textRects);
  if (lastTextRect) {
    rect.top = lastTextRect.top + lastTextRect.height;
    rect.left = lastTextRect.left + lastTextRect.width;
  }
  return rect;
}

export function getTooltipPositionWithNode(node) {
  const textNode = node.childNodes[0];
  const selectedRange = document.createRange();
  selectedRange.selectNode(textNode);
  return getTooltipPosition(selectedRange);
}

export function getTooltipPositionWithSelection(selection) {
  const selectedRange = selection.getRangeAt(selection.rangeCount - 1);
  return getTooltipPosition(selectedRange);
}

export function removeTooltip() {
  const currentTooltip = document.getElementById('additor-annotation');
  if (currentTooltip) {
    currentTooltip.remove();
  }
}

export function renderTooltip({
  targetLocalId,
  x,
  y,
  targetColor = null,
  removeHighlight = _.noop,
  changeColor = _.noop,
}) {
  removeTooltip();
  const targetColorName = getColorName(targetColor);
  const root = getAnnotationRoot();

  const container = document.createElement('div');
  container.id = 'additor-annotation';
  container.className = 'additor-annotation';

  const tooltip = document.createElement('div');
  tooltip.className = 'additor-annotation__tooltip';
  tooltip.style.left = `${x}px`;
  tooltip.style.top = `${y}px`;
  tooltip.addEventListener('mousedown', e => {
    // 실수로 툴팁영역을 클릭했을 때, selection 이 빠지지 않도록 처리
    e.preventDefault();
  });

  const buttonList = document.createElement('div');
  buttonList.className = 'additor-annotation__tooltip__button-list';
  _.forEach(Object.keys(HIGHLIGHT_COLORS), colorName => {
    const buttonItem = document.createElement('div');
    const isCurrentColor = targetColorName === colorName;
    buttonItem.className = classNames(
      'additor-annotation__tooltip__button-item',
      `additor-annotation__tooltip__button-item--${colorName}`,
      {
        'additor-annotation__tooltip__button-item--selected': isCurrentColor,
      },
    );

    const button = document.createElement('button');
    if (isCurrentColor) {
      button.innerHTML = `
        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16">
          <path fill="#FFF" fill-rule="nonzero" d="M8 7.293l2.828-2.829a.5.5 0 0 1 .708.708L8.707 8l2.829 2.828a.5.5 0 1 1-.708.708L8 8.707l-2.828 2.829a.5.5 0 1 1-.708-.708L7.293 8 4.464 5.172a.5.5 0 0 1 .708-.708L8 7.293z"/>
        </svg>
      `;
      button.addEventListener('click', () => removeHighlight(targetLocalId));
    } else {
      button.addEventListener('click', () => {
        changeColor(targetLocalId, colorName);
        setLastSelectedColorName(colorName);
      });
    }

    buttonItem.appendChild(button);
    buttonList.appendChild(buttonItem);
  });

  tooltip.appendChild(buttonList);
  container.appendChild(tooltip);
  root.appendChild(container);

  return container;
}
