import MessageTypes from 'common/MessageTypes';
import generateResponse from 'common/generateResponse';
import {
  showDisableHighlightAlert,
  showEnableHighlightAlert,
} from '../highlighter/highlightAlert';
import { openWidget, unmountWidget } from '../widget';
import { showSettings } from '../settings';
import { getStore } from '../redux/store';
import {
  updateInitialStates,
  disableWorkspace,
  updateWorkspaces,
  showErrorView,
} from '../redux/action';

/**
 * actions from background
 * 2. deleted card
 * 3. updated card title
 * 4. updated card tags
 * 5. updated card thumb
 * 6. updated page name
 * 7. updated channel name
 * 8. updated workspace name
 * 9. updated workspace thumb
 * 10. updated workspace
 * 11. created page
 * 12. deleted page
 * 12. created channel
 * 13. deleted channel
 * 13. added workspace
 * 14. remove workspace
 */

export function handleOpenWidget(request, sender, sendResponse) {
  openWidget();
  sendResponse(generateResponse());
}

export function handleCloseWidget(request, sender, sendResponse) {
  unmountWidget();
  sendResponse(generateResponse());
}

function handleShowSettings() {
  showSettings();
}

function handleUpdateInitialStates(request) {
  const store = getStore();
  if (!store) {
    return;
  }
  store.dispatch(updateInitialStates(request.data.states));
}

function handleDisableUserAction() {
  const store = getStore();
  store.dispatch(disableWorkspace());
}

function handleUpdateWorkspaces(request) {
  const store = getStore();
  store.dispatch(updateWorkspaces(request.data.workspaces));
}

export function handleShowErrorView() {
  const store = getStore();
  if (!store) {
    return;
  }
  store.dispatch(showErrorView());
}

export function handleDeleteCard() {}

export function handleUpdateCardTitle() {}

export function handleUpdateCardThumb() {}

export function handleUpdateCardTags() {}

export function handleUpdatePageName() {}

export function handleUpdateChannelName() {}

export function handleUpdateWorkspaceName() {}

export function handleUpdateWorkspaceThumb() {}

export function handleCreatePage() {}

export function handleDeletePage() {}

export function handleCreateChannel() {}

export function handleDeleteChannel() {}

export function handleAddWorkspace() {}

export function handleRemoveWorkspace() {}

export function handleMessages(request, sender, sendResponse) {
  switch (request.type) {
    case MessageTypes.OPEN_WIDGET:
      handleOpenWidget(request, sender, sendResponse);
      break;
    case MessageTypes.RESET_WIDGET:
      handleCloseWidget(request, sender, sendResponse);
      break;
    case MessageTypes.SHOW_SETTINGS:
      handleShowSettings();
      break;
    case MessageTypes.UPDATE_INITIAL_STATES:
      handleUpdateInitialStates(request);
      break;
    case MessageTypes.UPDATE_WORKSPACES:
      handleUpdateWorkspaces(request);
      break;
    case MessageTypes.SHOW_ENABLE_HIGHLIGHT_ALERT:
      showEnableHighlightAlert();
      break;
    case MessageTypes.SHOW_DISABLE_HIGHLIGHT_ALERT:
      showDisableHighlightAlert();
      break;
    case MessageTypes.DISABLE_USER_ACTION:
      handleDisableUserAction(request);
      break;
    case MessageTypes.SHOW_ERROR_VIEW:
      handleShowErrorView();
      break;
    default:
      break;
  }
}
