const popupClassName = 'additor-settings-popup';
const mainLogoSrc = chrome.runtime.getURL('images/logos/main_logo.png');

export default function createPopup({
  root,
  onClickSaveButton,
  onClickCancelButton,
  initialSettings,
}) {
  let nextSettings = { ...initialSettings };
  const settingsPopup = document.createElement('div');
  settingsPopup.classList.add(popupClassName);

  settingsPopup.innerHTML = `
    <img class="${popupClassName}__logo" src="${mainLogoSrc}">
    <div class="${popupClassName}__title">Extension Settings</div>
    <ul class="${popupClassName}__list">
      <li class="${popupClassName}__list-item">
        <div class="${popupClassName}__setting-texts">
          <div class="${popupClassName}__setting-texts__title">To turn on/off the highlighter</div>
          <div class="${popupClassName}__setting-texts__desc">The highlighter appears when you select texts.</div>
        </div>
        <div class="${popupClassName}__toggle-button-wrapper">
          <div class="${popupClassName}__toggle-button ${popupClassName}__toggle-button--on">On</div>
          <div class="${popupClassName}__toggle-button ${popupClassName}__toggle-button--off">Off</div>
        </div>
      </li>
    </ul>
    <div class="${popupClassName}__button-wrapper">
      <div class="${popupClassName}__button ${popupClassName}__button--cancel">Cancel</div>
      <div class="${popupClassName}__button ${popupClassName}__button--save">Save</div>
    </div>
  `;

  const highlightOnButton = settingsPopup.querySelector(
    `.${popupClassName}__toggle-button--on`,
  );
  const highlightOffButton = settingsPopup.querySelector(
    `.${popupClassName}__toggle-button--off`,
  );

  highlightOnButton.onclick = () => {
    nextSettings.highlightable = true;
    highlightOnButton.classList.add('selected');
    highlightOffButton.classList.remove('selected');
  };

  highlightOffButton.onclick = () => {
    nextSettings.highlightable = false;
    highlightOffButton.classList.add('selected');
    highlightOnButton.classList.remove('selected');
  };

  if (initialSettings && initialSettings.highlightable) {
    highlightOnButton.classList.add('selected');
  } else {
    highlightOffButton.classList.add('selected');
  }

  const cancelButton = settingsPopup.querySelector(
    `.${popupClassName}__button--cancel`,
  );
  const saveButton = settingsPopup.querySelector(
    `.${popupClassName}__button--save`,
  );
  cancelButton.onclick = onClickCancelButton;
  saveButton.onclick = () => {
    onClickSaveButton(nextSettings);
  };

  root.appendChild(settingsPopup);
}
