import _ from 'lodash';
import React from 'react';
import PropTypes from 'prop-types';

const HighlightContent = ({ children, keyword, highlightAll }) => {
  if (keyword === '') {
    return children;
  }

  const regexp = new RegExp(keyword, 'ig');

  const matchedIndexList = [];

  let value = null;
  while ((value = regexp.exec(children)) !== null) {
    matchedIndexList.push(value.index);
    if (!highlightAll) {
      break;
    }
  }

  if (matchedIndexList.length === 0) {
    return children;
  }

  if (!highlightAll) {
    _.dropRight(matchedIndexList, matchedIndexList.length - 1);
  }

  return (
    <>
      <>{children.substring(0, matchedIndexList[0])}</>
      {_.map(matchedIndexList, (matchedIndex, index, list) => (
        <React.Fragment key={index}>
          <strong>
            {children.substring(matchedIndex, matchedIndex + keyword.length)}
          </strong>
          {index < list.length - 1 && (
            <>
              {children.substring(
                matchedIndex + keyword.length,
                list[index + 1],
              )}
            </>
          )}
        </React.Fragment>
      ))}
      <>
        {children.substring(
          matchedIndexList[matchedIndexList.length - 1] + keyword.length,
        )}
      </>
    </>
  );
};

HighlightContent.propTypes = {
  children: PropTypes.string.isRequired,
  highlightAll: PropTypes.bool,
  keyword: PropTypes.string.isRequired,
};

HighlightContent.defaultProps = {
  highlightAll: false,
};

export default HighlightContent;
