import _ from 'lodash';

export function markNode(node, options = {}) {
  const {
    startOffset = 0,
    endOffset = node.length,
    onClick = _.noop,
    onMouseEnter = _.noop,
    onMouseLeave = _.noop,
    className = '',
    attrs = {},
  } = options;
  if (
    // node.data.match(/^[\r\n]*$/) ||
    node.data.match(/^\s*$/) ||
    startOffset === endOffset ||
    node.parentNode.tagName.toLowerCase() === 'mark'
  ) {
    return null;
  }
  // eslint-disable-next-line prefer-destructuring
  const parentNode = node.parentNode;
  const prevText = node.data.slice(0, startOffset);
  const markText = node.data.slice(startOffset, endOffset);
  const nextText = node.data.slice(endOffset);

  const container = document.createDocumentFragment();
  if (prevText) {
    container.appendChild(document.createTextNode(prevText));
  }
  const markElement = document.createElement('mark');
  markElement.className = className;
  markElement.appendChild(document.createTextNode(markText));
  markElement.addEventListener('click', onClick);
  markElement.addEventListener('mouseenter', onMouseEnter);
  markElement.addEventListener('mouseleave', onMouseLeave);

  _.forEach(Object.keys(attrs), attr => {
    markElement.setAttribute(attr, attrs[attr]);
  });
  container.appendChild(markElement);
  if (nextText) {
    container.appendChild(document.createTextNode(nextText));
  }
  parentNode.replaceChild(container, node);

  return markElement;
}

export function unmarkNode(marks) {
  while (marks.length > 0) {
    const text = marks[0].childNodes[0].data;
    if (
      marks[0].previousSibling != null &&
      marks[0].previousSibling.nodeType === 3
    ) {
      // eslint-disable-next-line no-param-reassign
      marks[0].previousSibling.data += text;
    } else if (
      marks[0].nextSibling != null &&
      marks[0].nextSibling.nodeType === 3
    ) {
      // eslint-disable-next-line no-param-reassign
      marks[0].nextSibling.data = text + marks[0].nextSibling.data;
    } else {
      marks[0].parentNode.insertBefore(document.createTextNode(text), marks[0]);
    }
    marks[0].remove();
  }
}

export function getTextNodesInRange(baseNode, startNode, endNode) {
  let pastStart = false;
  let reachedEnd = false;
  const textNodes = [];

  function getTextNodes(node) {
    if (!pastStart && node === startNode) {
      pastStart = true;
    }
    const inRange = pastStart && !reachedEnd;
    if (inRange && node.nodeType === 3) {
      textNodes.push(node);
    }
    if (
      inRange &&
      node.className != null &&
      node.className.split(' ').includes('additor-highlight')
    ) {
      throw new Error('Conflict with existing highlights.');
    }
    for (let i = 0; !reachedEnd && i < node.childNodes.length; i += 1) {
      getTextNodes(node.childNodes[i]);
    }
    if (!reachedEnd && node === endNode) {
      reachedEnd = true;
    }
  }

  getTextNodes(baseNode);
  return textNodes;
}
