import styled from 'styled-components/macro';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';

export const thumbnailBGColors = [
  '#FF9E7C',
  '#4BC7D0',
  '#83C189',
  '#9378FF',
  '#509DF6',
  '#C678FF',
  '#FF6C6C',
  '#FFA74B',
  '#BFC0C2',
];

export const WorkspaceTextThumbnail = styled.div`
  ${helpers.centerFlex}
  min-width: ${({ size }) => `${size}px`};
  width: ${({ size }) => `${size}px`};
  height: ${({ size }) => `${size}px`};
  font-size: 12px;
  font-weight: 500;
  color: ${colors.white};
  background-color: ${({ ctime = 0 }) => {
    const time = Math.floor(ctime / 1000);
    return thumbnailBGColors[time % thumbnailBGColors.length];
  }};
  margin-right: ${({ size }) => {
    if (size === 16 || size === 20) return '8px';
    else return '0';
  }};
  border-radius: 5px;
  cursor: pointer;
`;

export const WorkspaceImageThumbnail = styled.img`
  width: ${({ size }) => `${size}px`} !important;
  height: ${({ size }) => `${size}px`} !important;
  object-fit: cover;
  border-radius: 5px;
  margin-right: ${({ size }) => {
    if (size === 20) return '6px';
    else if (size === 16) return '8px';
    else return '0';
  }};
  cursor: pointer;
`;
