import _ from 'lodash';
import scrollWithAnimation from './scrollWithAnimation';
import {
  getColorCode,
  getHighlightContents,
  HIGHLIGHT_COLORS,
} from './highlightStore';

const highlightPopupClassName = 'additor-highlight-popup';

let scrollPointContainer;

export function getScrollPointHTMLElement(id) {
  if (!scrollPointContainer || !id) return null;
  return scrollPointContainer.querySelector(`[data-highlight-id="${id}"]`);
}

export function scrollToHighlight(id) {
  const marks = document.getElementsByClassName(`additor-highlight--${id}`);
  if (!_.isEmpty(marks)) {
    const highlightTop = marks[0].getBoundingClientRect().top;
    scrollWithAnimation(
      document.documentElement,
      highlightTop + window.scrollY - 120,
      200,
    );
  }
}

export function showHighlightPopup(id) {
  const scrollPoint = getScrollPointHTMLElement(id);
  const highlightPopup = document.createElement('DIV');
  const highlightContents = getHighlightContents(id);
  highlightPopup.classList.add(highlightPopupClassName);
  highlightPopup.setAttribute('data-highlight-id', id);
  const isBelowPopup = scrollPoint.offsetTop < window.innerHeight / 2;
  Object.assign(highlightPopup.style, {
    top: isBelowPopup ? 0 : 'initial',
    bottom: isBelowPopup ? 'initial' : 0,
  });
  highlightPopup.innerHTML = `
    <div class="additor-highlight-popup__contents" style="border-color:${scrollPoint.style.backgroundColor};">
      ${highlightContents}
    </div>
  `;
  scrollPoint.appendChild(highlightPopup);
}

export function hideHighlightPopup() {
  const highlightPopups = scrollPointContainer.getElementsByClassName(
    highlightPopupClassName,
  );
  _.forEach(highlightPopups, popup => {
    popup.remove();
  });
}

export function renderScrollPointContainer() {
  scrollPointContainer = document.createElement('DIV');
  scrollPointContainer.setAttribute('id', 'scroll-point-container');
  document.body.appendChild(scrollPointContainer);
}

export function generateRelativeTopOffset(topOffset) {
  let { innerHeight } = window;
  let scrollParent = document.body;
  return (topOffset / scrollParent.scrollHeight) * innerHeight;
}

function createScrollPointElement(id) {
  const alreadyRenderedScrollPoint = scrollPointContainer.querySelector(
    `[data-highlight-id="${id}"`,
  );
  if (alreadyRenderedScrollPoint) return alreadyRenderedScrollPoint;
  const scrollPoint = document.createElement('DIV');
  Object.assign(scrollPoint.style, {
    display: 'block',
    position: 'absolute',
    left: '0',
    width: '8px',
    height: '8px',
    cursor: 'pointer',
    borderRadius: '50%',
    userSelect: 'none',
  });
  scrollPoint.setAttribute('data-highlight-id', id);
  scrollPoint.addEventListener('click', () => {
    scrollToHighlight(id);
  });
  scrollPoint.addEventListener('mouseenter', () => {
    showHighlightPopup(id);
  });
  scrollPoint.addEventListener('mouseleave', () => {
    hideHighlightPopup();
  });
  return scrollPoint;
}

export function renderHighlightScrollPoint({ id, scrollPointHTMLElement }) {
  if (!scrollPointContainer) renderScrollPointContainer();
  const topOffset = getHighlightTopOffset(id);
  const color = getHighlightColor(id);
  const relativeTopOffset = generateRelativeTopOffset(topOffset);
  Object.assign(scrollPointHTMLElement.style, {
    top: `${relativeTopOffset - 2}px`,
    backgroundColor: color,
  });
  scrollPointContainer.appendChild(scrollPointHTMLElement);
}

export function removeHighlightScrollPoint(id) {
  const scrollPoint = scrollPointContainer.querySelector(
    `[data-highlight-id="${id}"]`,
  );
  if (scrollPoint) {
    scrollPoint.remove();
  }
}

export function getHighlightTopOffset(id) {
  const marks = document.getElementsByClassName(`additor-highlight--${id}`);
  if (!_.isEmpty(marks)) {
    let topOffset = _.last(marks).offsetTop;
    return topOffset;
  }
  return -1;
}

export function getHighlightColor(id) {
  const marks = document.getElementsByClassName(`additor-highlight--${id}`);
  if (!_.isEmpty(marks)) {
    const colorName = marks[0].getAttribute('data-color');
    return getColorCode(colorName);
  }
  return HIGHLIGHT_COLORS.purple;
}

export function updateScrollPointColor({ id, color }) {
  if (!scrollPointContainer) return;
  const scrollPointer = getScrollPointHTMLElement(id);
  Object.assign(scrollPointer.style, {
    backgroundColor: color,
  });
}

export function createAndRenderHighlightScrollPoint(id) {
  if (!scrollPointContainer) renderScrollPointContainer();
  const highlightScrollPointElement = createScrollPointElement(id);
  renderHighlightScrollPoint({
    id,
    scrollPointHTMLElement: highlightScrollPointElement,
  });
}

export function createAllHighlightScrollPoints() {
  const marks = document.getElementsByClassName('additor-highlight');
  _.forEach(marks, mark => {
    const highlightId = mark.getAttribute('data-highlight-id');
    createAndRenderHighlightScrollPoint(highlightId);
  });
}
