import { Authority } from '@additor/elements';

import {
  sendMessageToCreateCard,
  sendMessageToMoveCard,
  sendMessageToDeleteCard,
  sendMessageToSwitchWorkspace,
  sendMessageToResetWidgetOpenedTabId,
  sendMessageToSetWidgetOpenedTabId,
  sendMessageToUpdateCardTags,
  sendMessageToUpdateCardTitle,
  sendMessageToUpdateCardThumbnail,
  sendMessageToCreateFolder,
} from '../../messagePassing/sendMessage';
import actionTypes from './actionTypes';
import { getStore } from '../store';

export const createCard = dispatch => ({ cardUri, parent = null }) => {
  sendMessageToCreateCard({ cardUri, parent }).then(response => {
    setTimeout(() => {
      dispatch({
        type: actionTypes.CREATE_CARD_SUCCESS,
        response,
      });
    }, 380);
  });

  dispatch({
    type: actionTypes.CREATE_CARD,
  });
};

export const deleteCard = dispatch => () => {
  sendMessageToDeleteCard().then(response => {
    setTimeout(() => {
      dispatch({
        type: actionTypes.DELETE_CARD_SUCCESS,
        response,
      });
    }, 380);
  });

  dispatch({
    type: actionTypes.DELETE_CARD,
  });
};

export const createFolder = dispatch => ({ name, shouldMoveCard }) => {
  dispatch({
    type: actionTypes.CREATE_FOLDER,
  });

  return sendMessageToCreateFolder({ name, shouldMoveCard }).then(response => {
    dispatch({
      type: actionTypes.CREATE_FOLDER_SUCCESS,
      response,
    });
    return response;
  });
};

export const moveCard = dispatch => ({ cardId, parent = null }) => {
  sendMessageToMoveCard({ cardId, parent }).then(response => {
    setTimeout(() => {
      dispatch({
        type: actionTypes.MOVE_CARD_SUCCESS,
        response,
      });
    }, 380);
  });

  dispatch({
    type: actionTypes.MOVE_CARD,
  });
};

export const switchWorkspace = dispatch => id => {
  sendMessageToSwitchWorkspace(id).then(response => {
    if (!response.ok) {
      dispatch({
        type: actionTypes.SWITCH_WORKSPACE_FAIL,
        workspaceId: response.data.workspaceId,
      });
    } else {
      dispatch({
        type: actionTypes.SWITCH_WORKSPACE_SUCCESS,
        id,
        response,
      });
      const {
        card: { cardUri },
        widget: { myRole, recentFolderId },
      } = getStore().getState();
      const isGuest = myRole === Authority.EntireRoles['GUEST'];
      if (!isGuest) {
        sendMessageToCreateCard({ cardUri, parent: recentFolderId }).then(
          response => {
            setTimeout(() => {
              dispatch({
                type: actionTypes.CREATE_CARD_SUCCESS,
                response,
              });
            }, 380);
          },
        );

        dispatch({
          type: actionTypes.CREATE_CARD,
        });
      }
    }
  });

  dispatch({
    type: actionTypes.SWITCH_WORKSPACE,
    id,
  });
};

export function setSaving() {
  return {
    type: actionTypes.SAVING,
  };
}

export function setSaved() {
  return {
    type: actionTypes.SAVED,
  };
}

export function showWidget() {
  sendMessageToSetWidgetOpenedTabId();
  return {
    type: actionTypes.SHOW_WIDGET,
  };
}

export function hideWidget() {
  sendMessageToResetWidgetOpenedTabId();
  return {
    type: actionTypes.HIDE_WIDGET,
  };
}

export function updateInitialStates(states) {
  return {
    type: actionTypes.UPDATE_INITIAL_STATES,
    states,
  };
}

export function updateWorkspaces(workspaces) {
  return {
    type: actionTypes.UPDATE_WORKSPACES,
    workspaces,
  };
}

export function disableWorkspace() {
  return {
    type: actionTypes.DISABLE_WORKSPACE,
    disabledType: true,
  };
}

export function showErrorView() {
  return {
    type: actionTypes.SHOW_ERROR_VIEW,
  };
}

