import React, { useCallback } from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components/macro';
import colors from '../../../common/themes/colors';
import helpers from '../../../common/themes/helpers';
import { sendMessageToReloadApp } from '../../messagePassing/sendMessage';
import SvgDisable from '../Widget/icons/disable.svg';

const Wrapper = styled.div`
  display: ${({ isHidden }) => (isHidden ? 'none' : 'block')};
  width: 335px;
  height: 373px;
  position: fixed;
  top: 10px;
  right: 10px;
  background: #fff;
  z-index: 2147483631;
  border-radius: 5px;
  box-shadow: 0 4px 24px -4px rgba(0, 0, 0, 0.18);
  font-size: 1rem;
  color: ${colors.gray900};
  font-family: 'system-ui', 'Open Sans', 'Apple SD Gothic Neo', 'Noto Sans KR',
    sans-serif;
`;

const UserActionBlockerWrapper = styled.div`
  ${helpers.centerFlex}
  flex-direction: column;
  width: 100%;
  height: 100%;
  background-color: ${colors.gray100};
  opacity: 0.95;
  border-radius: 5px;
  z-index: 1;

  span {
    ${helpers.centerFlex}
    position: absolute;
    top: 16px;
    right: 24px;
    color: ${colors.red400};
    font-size: 13px;
    font-weight: 500;
  }

  svg {
    color: ${colors.red400};
    margin-left: 6px;
  }

  h1 {
    margin: 0 0 20px 0;
    padding: 0;
    font-size: 18px;
    font-weight: 600;
    color: ${colors.gray900};
  }

  p {
    margin: 0 20px 2px 20px;
    padding: 0;
    font-size: 14px;
    color: ${colors.gray900};
    white-space: pre-line;
    line-height: 1.5;
  }

  button {
    ${helpers.forceInitialize}
    ${helpers.forceCenterFlex}
    margin-top: 25px !important;
    width: 67px !important;
    height: 30px !important;
    font-size: 13px !important;
    font-weight: 500 !important;
    color: ${colors.white} !important;
    background-color: ${colors.brand500} !important;
    cursor: pointer !important;
    border-radius: 3px !important;
    &:hover {
      background-color: ${colors.brand700} !important;
    }
    &:active {
      background-color: ${colors.brand800} !important;
    }
  }
`;

const ErrorView = ({
  title = 'Oops!',
  description = 'Something went wrong.\nPlease press the button below to refresh the app.',
  onClick,
  buttonText = 'Refresh',
}) => {
  const onClickRefreshButton = useCallback(() => {
    sendMessageToReloadApp();
  }, []);
  return (
    <Wrapper>
      <UserActionBlockerWrapper>
        <span>
          Unaccessible
          <SvgDisable />
        </span>
        <h1>{title}</h1>
        <p>{description}</p>
        <button onClick={onClick ? onClick : onClickRefreshButton}>
          {buttonText}
        </button>
      </UserActionBlockerWrapper>
    </Wrapper>
  );
};

ErrorView.propTypes = {
  title: PropTypes.node,
  description: PropTypes.node,
  onClick: PropTypes.func,
  buttonText: PropTypes.string,
};
export default ErrorView;
