import React, { useState, useCallback } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { connect } from 'react-redux';
import styled from 'styled-components/macro';
import getHostName from 'common/getHostName';
import colors from 'common/themes/colors';
import { switchWorkspace } from 'contentScript/redux/action';
import { getStore } from 'contentScript/redux/store';
import helpers from 'common/themes/helpers';
import SvgNewTab from '../icons/newtab.svg';
import SvgToBottom from '../icons/tobottom.svg';
import {
  WorkspaceTextThumbnail,
  WorkspaceImageThumbnail,
} from './WorkspaceThumbnails';
import WorkspaceMenuPopup from './MenuPopup';

const WorkspaceSelectorWrapper = styled.div`
  width: 100%;
  padding-left: 23px;
  padding-right: 16px;
  height: 52px;
  box-shadow: 0 -1px 0 0 ${colors.gray80};
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;

  & > div {
    ${helpers.centerFlex}
  }
`;

const WorkspaceSwitchButton = styled.div`
  ${helpers.centerFlex}
  height: 30px;
  max-width: 180px;
  padding: 0 2px 0 6px;
  border-radius: 3px;
  cursor: pointer;
  &:hover {
    background-color: ${colors.gray80};
    svg {
      color: ${colors.gray700};
    }
  }

  h1 {
    margin: 0;
    padding: 0;
    font-size: 13px;
    font-weight: 500;
    color: ${colors.gray700};
    white-space: nowrap;
    max-width: 125px;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    line-height: 18px;
  }
`;

const OpenAdditorButton = styled.button`
  ${helpers.forceInitialize}
  ${helpers.forceCenterFlex}
  width: 112px !important;
  height: 24px !important;
  padding-left: 8px !important;
  padding-right: 6px !important;
  border-radius: 3px !important;
  border: none !important;
  background-color: ${colors.white} !important;
  font-size: 12px !important;
  font-weight: 500 !important;
  color: ${colors.gray500} !important;
  cursor: pointer !important;
  ${helpers.transition('color', 'background-color')}

  svg {
    margin-left: 4px !important;
    margin-top: 1px !important;
    path {
      ${helpers.transition('fill')}
    }
  }

  &:hover {
    color: ${colors.gray700} !important;
    background-color: ${colors.gray80} !important;
    svg path {
      fill: ${colors.gray700} !important;
    }
  }
`;

const DropdownButton = styled.div`
  margin-left: 4px;
  height: 20px;
  width: 20px;
  border-radius: 3px;
  cursor: pointer;
  ${helpers.centerFlex}
  ${helpers.transition('box-shadow', 'background-color')}

  svg {
    color: ${colors.gray500};
    ${helpers.transition('color')}
  }
`;

function WorkspaceSelector({
  workspaceList,
  currentWorkspaceId,
  currentWorkspaceName,
  currentWorkspaceThumbnail,
  currentWorkspaceCtime,
  switchWorkspace,
}) {
  const [menuPopupVisible, setMenuPopupVisible] = useState(false);

  const closeMenuPopup = useCallback(() => setMenuPopupVisible(false), []);
  const toggleMenuPopup = useCallback(() => {
    setMenuPopupVisible(!menuPopupVisible);
  }, []);

  const handleClickOutside = useCallback(() => {
    closeMenuPopup();
  }, []);

  const handleClickMenuItem = useCallback(id => {
    switchWorkspace(id);
    closeMenuPopup();
  }, []);

  const handleClickOpenAdditorButton = useCallback(() => {
    const store = getStore();
    const globalState = store.getState();
    const {
      widget: { workspaces, currentWorkspaceId },
    } = globalState;

    if (!currentWorkspaceId) {
      return;
    }

    const currentWorkspace = _.find(
      workspaces,
      ({ id }) => id === currentWorkspaceId,
    );

    window.open(`https://${currentWorkspace.eid}.${getHostName()}`);
  }, []);

  return (
    <WorkspaceSelectorWrapper>
      <div>
        <WorkspaceSwitchButton onClick={toggleMenuPopup}>
          {currentWorkspaceThumbnail === '' ? (
            <WorkspaceTextThumbnail ctime={currentWorkspaceCtime} size={20}>
              {_.toUpper(_.head(currentWorkspaceName))}
            </WorkspaceTextThumbnail>
          ) : (
            <WorkspaceImageThumbnail
              src={currentWorkspaceThumbnail}
              alt={currentWorkspaceName}
              size={20}
            />
          )}
          <h1>{currentWorkspaceName}</h1>
          <DropdownButton>
            <SvgToBottom />
          </DropdownButton>
        </WorkspaceSwitchButton>
        {menuPopupVisible && (
          <WorkspaceMenuPopup
            workspaceList={workspaceList}
            currentWorkspaceId={currentWorkspaceId}
            onClickMenuItem={handleClickMenuItem}
            onClickOutside={handleClickOutside}
          />
        )}
      </div>
      <OpenAdditorButton onClick={handleClickOpenAdditorButton}>
        Open Home
        <SvgNewTab />
      </OpenAdditorButton>
    </WorkspaceSelectorWrapper>
  );
}

WorkspaceSelector.propTypes = {
  workspaceList: PropTypes.array.isRequired,
  currentWorkspaceId: PropTypes.string.isRequired,
  currentWorkspaceName: PropTypes.string.isRequired,
  currentWorkspaceThumbnail: PropTypes.string.isRequired,
  currentWorkspaceCtime: PropTypes.number.isRequired,
  switchWorkspace: PropTypes.func.isRequired,
};

const mapStateToProps = state => {
  const {
    widget: {
      currentWorkspaceId,
      workspaces,
      signedOutWorkspace,
      disableWorkspace,
    },
  } = state;

  let workspaceList = _.map(
    workspaces,
    ({ id, eid, name, utime, ctime, images }) => ({
      id,
      eid,
      name,
      utime,
      ctime,
      thumbnail: _.get(images, 'thumb'),
    }),
  );

  const currentWorkspace = _.find(workspaceList, { id: currentWorkspaceId });
  if (signedOutWorkspace || disableWorkspace) {
    _.remove(workspaceList, ({ id }) => id === currentWorkspaceId);
  }

  return {
    workspaceList,
    currentWorkspaceId,
    currentWorkspaceName: currentWorkspace.name,
    currentWorkspaceThumbnail: currentWorkspace.thumbnail || '',
    currentWorkspaceCtime: currentWorkspace.ctime,
  };
};

const mapDispatchToProps = dispatch => ({
  switchWorkspace: switchWorkspace(dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(WorkspaceSelector);
