import _ from 'lodash';

function isRelativePath(path) {
  return _.startsWith(path, '/');
}

function convertRelativePathToAbsolutePath(path) {
  if (isRelativePath(path)) {
    return `${window.location.origin}${path}`;
  }
  return path;
}

function extractThumbnails() {
  const imageNods = document.getElementsByTagName('img');
  const normalizedImages = _.filter(imageNods, node => {
    return node.width > 100 && !_.startsWith(node.src, 'data:');
  });
  const imagesSortedByWidth = _.reverse(
    _.sortBy(normalizedImages, node => node.width),
  );

  return _.map(imagesSortedByWidth, node => node.src);
}

function extractFavicon() {
  let targetNode = document.querySelector('link[rel="icon"]');
  if (targetNode) {
    return convertRelativePathToAbsolutePath(targetNode.href);
  }

  targetNode = document.querySelector('link[rel="shortcut\\ icon"]');
  if (targetNode) {
    return convertRelativePathToAbsolutePath(targetNode.href);
  }

  targetNode = document.querySelector('link[rel="apple-touch-icon"]');
  if (targetNode) {
    return convertRelativePathToAbsolutePath(targetNode.href);
  }

  targetNode = document.querySelector('link[rel="mask-icon"]');
  if (targetNode) {
    return convertRelativePathToAbsolutePath(targetNode.href);
  }

  return null;
}

function extractURI() {
  return window.location.href;
}

export default async function extractBaseCardData() {
  return {
    thumbnails: extractThumbnails(),
    favicon: extractFavicon(),
    uri: extractURI(),
  };
}
