let timeoutKey = null;

function removeHighlightAlert() {
  if (timeoutKey) {
    clearTimeout(timeoutKey);
    timeoutKey = null;
  }
  const alertElement = document.getElementsByClassName(
    'additor-highlight-alert',
  )[0];
  if (alertElement) {
    alertElement.remove();
  }
}

export function showEnableHighlightAlert() {
  removeHighlightAlert();
  const baseElement = document.getElementById('additor-crx-base');
  const alertElement = document.createElement('div');
  alertElement.className =
    'additor-highlight-alert additor-highlight-alert--on';
  alertElement.innerText = 'Highlight ON';
  baseElement.appendChild(alertElement);
  timeoutKey = setTimeout(() => {
    removeHighlightAlert();
  }, 2000);
}

export function showDisableHighlightAlert() {
  removeHighlightAlert();
  const baseElement = document.getElementById('additor-crx-base');
  const alertElement = document.createElement('div');
  alertElement.className =
    'additor-highlight-alert additor-highlight-alert--off';
  alertElement.innerText = 'Highlight OFF';
  baseElement.appendChild(alertElement);
  timeoutKey = setTimeout(() => {
    removeHighlightAlert();
  }, 2000);
}
