import React, { useState, useCallback, useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { connect } from 'react-redux';
import styled from 'styled-components/macro';
import AutosizeInput from 'react-input-autosize';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';
import SvgX from './icons/x.svg';
import { updateCardTags } from 'contentScript/redux/action';

const TagsWrapper = styled.ul`
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  margin: 0 16px 0 24px;
  padding: 0;
  margin-bottom: 8px;
  font-size: 13px;
  color: ${colors.gray400};
  max-height: 114px;
  overflow-y: auto;
`;

const StyledTagItem = styled.li`
  ${helpers.centerFlex}
  min-width: 56px;
  height: 24px;
  margin: 0 8px 8px 0;
  padding: 0;
  border-radius: 16px;
  backdrop-filter: blur(2px);
  background-color: ${colors.gray50};

  & > div {
    display: flex !important;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 0 5px;
  }

  input {
    background: transparent !important;
    border: none !important;
    color: ${colors.gray600} !important;
    caret-color: ${colors.gray500} !important;
    box-shadow: none !important;
    font-weight: normal !important;
    padding: 2px 5px !important;

    &::placeholder {
      ${helpers.forceInitialize}
      color: ${colors.gray400} !important;
    }
  }
`;

const DeleteTagButton = styled.button`
  ${helpers.forceInitialize}
  ${helpers.forceCenterFlex}
  width: 16px !important;
  height: 16px !important;
  padding: 0 !important;
  margin-left: -10px !important;
  margin-right: 5px !important;
  border-radius: 50% !important;
  background-color: ${colors.white} !important;
  cursor: pointer !important;
  svg path {
    fill: ${colors.gray600};
  }
`;

const TagItem = ({ tag, onSubmit, index = -1 }) => {
  const isMouseDownOnDeleteBtn = useRef(false);
  const [currentTag, setCurrentTag] = useState(tag);
  const [deleteButtonVisible, setDeleteButtonVisible] = useState(false);

  const handleInputChange = useCallback(e => {
    setCurrentTag(e.target.value);
  }, []);

  const inputRef = useRef(null);

  const handleKeyDown = useCallback(e => {
    const TAB = 9;
    const ENTER = 13;
    if (e.keyCode === ENTER) {
      e.preventDefault();
      e.target.blur();
      inputRef.current.focus();
    } else if (e.keyCode === TAB) {
      e.preventDefault();
      e.target.blur();
      inputRef.current.focus();
    }
  }, []);

  const handleBlur = useCallback(() => {
    if (isMouseDownOnDeleteBtn.current) {
      onSubmit(index, '');
      setDeleteButtonVisible(false);
      return;
    }
    if (index < 0) {
      onSubmit(currentTag); // createTag
      setCurrentTag('');
    } else {
      onSubmit(index, currentTag); // updateTag
    }
    setDeleteButtonVisible(false);
  }, [currentTag, onSubmit]);

  const handleFocus = useCallback(() => {
    setDeleteButtonVisible(true);
  }, []);

  const handleDelete = useCallback(() => {
    // react-select 가 있는 경우 mouseDown 에서 아래 코드 실행 시 익스텐션 팝업이 닫히고 태그 인풋 width 가 작아지는 버그가 있음
    // 이에 handleBlur 에서 처리하도록 변경
    // onSubmit(index, '');
    // setDeleteButtonVisible(false);
    isMouseDownOnDeleteBtn.current = true;
  }, [onSubmit]);

  const setInputRef = el => {
    inputRef.current = el;
  };

  useEffect(() => {
    setCurrentTag(tag);
  }, [tag]);

  // NOTE: Input 의 onBlur 에서 x button 을 숨기기 때문에
  // DeleteTagButton 에서 onClick 보다 더 빠르게 이벤트를 받을 수 있는 onMouseDown 을 이용
  return (
    <StyledTagItem>
      <AutosizeInput
        spellCheck="false"
        ref={setInputRef}
        type="text"
        placeholder={tag === '' ? '+Tag' : ''}
        maxLength={25}
        value={currentTag}
        onChange={handleInputChange}
        inputStyle={{
          fontSize: 13,
        }}
        onKeyDown={handleKeyDown}
        onFocus={handleFocus}
        onBlur={handleBlur}
      />
      {index >= 0 && deleteButtonVisible && (
        <DeleteTagButton onMouseDown={handleDelete}>
          <SvgX />
        </DeleteTagButton>
      )}
    </StyledTagItem>
  );
};

TagItem.propTypes = {
  tag: PropTypes.string.isRequired,
  onSubmit: PropTypes.func.isRequired,
  index: PropTypes.number,
};

function Tags({ cardTags, updateCardTags, blockUserAction }) {
  const [prevTags, setPrevTags] = useState(cardTags);
  const [tags, setTags] = useState(cardTags);

  const createTag = useCallback(
    tag => {
      if (blockUserAction) return;
      if (_.trim(tag) === '') {
        return;
      }
      if (!_.includes(tags, tag)) {
        if (tags.length === 0) {
          setTags([tag]);
        } else {
          setTags([...tags, tag]);
        }
      }
    },
    [tags, blockUserAction],
  );

  const updateTag = useCallback(
    (i, tag = '') => {
      if (blockUserAction) return;
      if (tag === tags[i]) return;
      const newTags = _.clone(tags);
      newTags[i] = tag;
      setPrevTags(tags);
      setTags(_.filter(newTags, tag => tag !== ''));
    },
    [tags, blockUserAction],
  );

  useEffect(() => {
    if (tags !== prevTags) {
      updateCardTags(tags);
    }
  }, [tags, prevTags]);

  return (
    <TagsWrapper>
      {_.map(tags, (tag, i) => (
        <TagItem tag={tag} key={i} index={i} onSubmit={updateTag} />
      ))}
      {tags.length < 10 && <TagItem tag="" onSubmit={createTag} />}
    </TagsWrapper>
  );
}

Tags.propTypes = {
  cardTags: PropTypes.array.isRequired,
  updateCardTags: PropTypes.func.isRequired,
  blockUserAction: PropTypes.bool.isRequired,
};

const mapStateToProps = state => {
  const {
    card: { cardTags },
    widget: { blockUserAction },
  } = state;

  return {
    cardTags,
    blockUserAction,
  };
};

const mapDispatchToProps = dispatch => ({
  updateCardTags: updateCardTags(dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Tags);
