import _ from 'lodash';

function getXPathNodeName(node) {
  const nodeName = node.nodeName.toLowerCase();
  switch (nodeName) {
    case '#text':
      return 'text()';
    case '#comment':
      return 'comment()';
    case '#cdata-section':
      return 'cdata-section()';
    default:
      return nodeName;
  }
}

function isXML(xmlStr) {
  let parseXml;

  if (typeof window.DOMParser !== 'undefined') {
    parseXml = _xmlStr =>
      new window.DOMParser().parseFromString(_xmlStr, 'text/xml');
  } else if (
    typeof window.ActiveXObject !== 'undefined' &&
    new window.ActiveXObject('Microsoft.XMLDOM')
  ) {
    parseXml = _xmlStr => {
      const xmlDoc = new window.ActiveXObject('Microsoft.XMLDOM');
      xmlDoc.async = 'false';
      xmlDoc.loadXML(_xmlStr);
      return xmlDoc;
    };
  } else {
    return false;
  }
  try {
    parseXml(xmlStr);
  } catch (e) {
    return false;
  }
  return true;
}

function findChild(node, type, index) {
  if (!node.hasChildNodes()) {
    throw new Error('XPath error: node has no children!');
  }
  const children = node.childNodes;
  let found = 0;
  for (let i = 0; i < children.length; i += 1) {
    const child = children[i];
    const name = getXPathNodeName(child);
    if (name === type) {
      found += 1;
      if (found === index) {
        return child;
      }
    }
  }
  throw new Error('XPath error: wanted child not found.');
}

function evaluateXPath(path, nsResolver) {
  try {
    return document.evaluate(
      path,
      document,
      nsResolver,
      window.XPathResult.FIRST_ORDERED_NODE_TYPE,
      null,
    ).singleNodeValue;
  } catch (_error) {
    const steps = path.substring(1).split('/');
    let node = document;
    for (let i = 0; i < steps.length; i += 1) {
      const step = steps[i];
      const ref = step.split('[');
      const name = ref[0];
      const idx = ref[1] ? ref[1].split(']')[0] : 1;
      node = findChild(node, name.toLowerCase(), idx);
    }
    return node;
  }
}

export function getNodeByPath(path) {
  if (!isXML(document.documentElement)) {
    return evaluateXPath(path, null);
  }
  let node = evaluateXPath(
    path,
    document.createNSResolver(
      document.ownerDocument === null
        ? document.documentElement
        : document.ownerDocument.documentElement,
    ),
  );
  if (!node) {
    const newPath = _.map(path.split('/'), n =>
      n.indexOf(':') === -1 ? n.replace(/^([a-z]+)/, 'xhtml:$1') : n,
    );
    const newResolver = ns =>
      ns === 'xhtml'
        ? document.lookupNamespaceURI(null)
        : document.documentElement.getAttribute(`xmlns:${ns}`);
    node = evaluateXPath(newPath, newResolver);
  }
  return node;
}

export function createXPathTo(element) {
  if (!element || element === document || element === document.body) {
    return '/';
  }

  const siblings = element.parentNode.childNodes;
  const xPathNodeName = getXPathNodeName(element);
  let i = 0;
  let xPathIndex = 1;
  while (i <= siblings.length) {
    if (siblings[i] === element) {
      return `${createXPathTo(
        element.parentNode,
      )}/${xPathNodeName}[${xPathIndex}]`;
    }
    if (xPathNodeName === getXPathNodeName(siblings[i])) {
      xPathIndex += 1;
    }
    i += 1;
  }

  return null;
}
