function translate(rect, x, y) {
  return {
    left: rect.left + x,
    top: rect.top + y,
    width: rect.width,
    height: rect.height,
  };
}

function mapViewportRectToDocument(window, rect) {
  // `pageXOffset` and `pageYOffset` are used rather than `scrollX`
  // and `scrollY` for IE 10/11 compatibility.
  return translate(rect, window.pageXOffset, window.pageYOffset);
}

function isNodeInRange(range, node) {
  if (node === range.startContainer || node === range.endContainer) {
    return true;
  }

  const nodeRange = node.ownerDocument.createRange();
  nodeRange.selectNode(node);
  const isAtOrBeforeStart =
    range.compareBoundaryPoints(Range.START_TO_START, nodeRange) <= 0;
  const isAtOrAfterEnd =
    range.compareBoundaryPoints(Range.END_TO_END, nodeRange) >= 0;
  nodeRange.detach();
  return isAtOrBeforeStart && isAtOrAfterEnd;
}

function forEachNodeInRange(range, callback) {
  const root = range.commonAncestorContainer;

  // The `whatToShow`, `filter` and `expandEntityReferences` arguments are
  // mandatory in IE although optional according to the spec.
  const nodeIter = root.ownerDocument.createNodeIterator(
    root,
    NodeFilter.SHOW_ALL,
    null /* filter */,
    false /* expandEntityReferences */,
  );

  let currentNode;
  while ((currentNode = nodeIter.nextNode())) {
    // eslint-disable-line no-cond-assign
    if (isNodeInRange(range, currentNode)) {
      callback(currentNode);
    }
  }
}

export function calculateTextRangeRects(range) {
  const whitespaceOnly = /^\s*$/;
  const textNodes = [];

  forEachNodeInRange(range, node => {
    if (
      node.nodeType === Node.TEXT_NODE &&
      !node.textContent.match(whitespaceOnly)
    ) {
      textNodes.push(node);
    }
  });

  let rects = [];
  let nextStartNode = false;
  textNodes.forEach((node, index) => {
    const nodeRange = node.ownerDocument.createRange();
    nodeRange.selectNodeContents(node);
    if (node === range.startContainer || nextStartNode) {
      let startOffset = nextStartNode ? 0 : range.startOffset;

      if (node !== range.endContainer || startOffset + 1 < range.endOffset) {
        if (node.length <= startOffset) {
          nextStartNode = true;
          return;
        }
        startOffset += 1;
        nextStartNode = false;
      }
      nodeRange.setStart(node, startOffset);
    }
    if (node === range.endContainer) {
      nodeRange.setEnd(node, range.endOffset);
    }
    if (nodeRange.collapsed) {
      // If the range ends at the start of this text node or starts at the end
      // of this node then do not include it.
      return;
    }

    // Measure the range and translate from viewport to document coordinates
    const viewportRects = Array.from(nodeRange.getClientRects());
    nodeRange.detach();
    rects = rects.concat(
      viewportRects.map(rect =>
        mapViewportRectToDocument(node.ownerDocument.defaultView, rect),
      ),
    );
  });
  return rects;
}
