import _ from 'lodash';
import {
  sendMessageToCreateHighlight,
  sendMessageToUpdateHighlightColor,
  sendMessageToDeleteHighlight,
  sendMessageToGetSettings,
} from '../messagePassing/sendMessage';
import { openWidget } from '../widget';
import {
  setLocalHighlight,
  getLocalHighlight,
  getLastSelectedColorCode,
  createHighlightWithSelection,
  hasHighlight,
  getColorCode,
  getAllHighlightIds,
} from './highlightStore';
import {
  renderTooltip,
  removeTooltip,
  getTooltipPositionWithSelection,
  getTooltipPositionWithNode,
} from './annotationTooltip';
import {
  markHighlightText,
  changeHighlightColor,
  removeHighlight,
} from './highlight';
import {
  createAndRenderHighlightScrollPoint,
  updateScrollPointColor,
  removeHighlightScrollPoint,
} from './highlightScrollPoint';
import { getStore } from 'contentScript/redux/store';
import actionTypes from 'contentScript/redux/action/actionTypes';

function renderHighlightTooltip(highlightId) {
  const markNodes = document.querySelectorAll(
    `.additor-highlight--${highlightId}`,
  );
  const rect = getTooltipPositionWithNode(_.last(markNodes));
  renderTooltip({
    targetLocalId: highlightId,
    x: rect.left,
    y: rect.top + 8,
    targetColor: getLocalHighlight(highlightId).color,
    changeColor: (highlightId, colorName) => {
      changeHighlightColor(highlightId, colorName);
      sendMessageToUpdateHighlightColor(highlightId, getColorCode(colorName));
      renderHighlightTooltip(highlightId);
      updateScrollPointColor({
        id: highlightId,
        color: getColorCode(colorName),
      });
    },
    removeHighlight: async highlightId => {
      removeHighlight(highlightId);
      await sendMessageToDeleteHighlight(highlightId);

      const { dispatch } = getStore();
      dispatch({
        type: actionTypes.DELETE_HIGHLIGHT_SUCCESS,
        highlightsLength: getAllHighlightIds().length || 0,
      });

      removeTooltip();
      removeHighlightScrollPoint(highlightId);
    },
  });
}

async function openWidgetAndCreateHighlight(textSelection) {
  // 애딧 생성 후 하이라이트 생성
  await openWidget();
  const highlightElement = createHighlightWithSelection(
    textSelection,
    getLastSelectedColorCode(),
  );
  if (!highlightElement) return;
  const response = await sendMessageToCreateHighlight(highlightElement);
  const {
    data: { highlight: createdHighlight },
  } = response;
  setLocalHighlight(createdHighlight);
  const highlightId = createdHighlight.id;
  markHighlightText({
    highlight: createdHighlight,
    onClick: () => {
      renderHighlightTooltip(highlightId);
    },
  });
  renderHighlightTooltip(highlightId);
  createAndRenderHighlightScrollPoint(highlightId);
  const selection = document.getSelection();
  selection.collapse(selection.focusNode);

  const { dispatch } = getStore();
  dispatch({
    type: actionTypes.CREATE_HIGHLIGHT_SUCCESS,
    highlightsLength: getAllHighlightIds().length || 0,
  });
}

function isHighlightableNode(node) {
  return !(
    node.closest('#additor-annotation') ||
    node.closest('#additor-crx-base') ||
    node.tagName === 'INPUT' ||
    node.tagName === 'TEXTAREA' ||
    node.isContentEditable
  );
}

async function handleMouseUp(e) {
  if (!isHighlightableNode(e.target)) {
    return;
  }

  e.preventDefault();
  if (!e.target.closest('.additor-highlight')) {
    removeTooltip();
  }

  const {
    data: { settings },
  } = await sendMessageToGetSettings();
  if (!settings.highlightable) {
    return;
  }

  const textSelection = window.getSelection();
  if (!textSelection || !_.trim(textSelection.toString())) {
    return;
  }

  if (hasHighlight()) {
    openWidgetAndCreateHighlight(textSelection);
  } else {
    const rect = getTooltipPositionWithSelection(textSelection);
    renderTooltip({
      x: rect.left,
      y: rect.top + 8,
      changeColor: () => {
        openWidgetAndCreateHighlight(textSelection);
      },
    });
  }
}

function handleKeyDown(e) {
  if (document.querySelector('.additor-highlight') && e.keyCode === 27) {
    removeTooltip();
  }
}

export default function activate() {
  window.addEventListener('mouseup', handleMouseUp);
  window.addEventListener('keydown', handleKeyDown);
}
