import { Authority } from '@additor/elements';
import React, { useCallback, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import _ from 'lodash';
import styled from 'styled-components/macro';

import { deleteCard } from 'contentScript/redux/action';
import { sendMessageToResetWidgetOpenedTabId } from '../../messagePassing/sendMessage';
import { unmountWidget } from '../../widget';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';
import getHostName from 'common/getHostName';

import SvgCheck from './icons/check.svg';
import SvgRightArrow from './icons/right-arrow.svg';
import SvgUndo from './icons/undo.svg';
import SvgProgress from './icons/progress.svg';
import SvgCloseX from './icons/closeX.svg';

const WidgetHeader = styled.div`
  height: 52px;
  margin: 0 24px 0 16px;
  font-size: 13px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: space-between;

  div {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    &.saving,
    &.saved {
      color: ${colors.brand500};
    }
    &.undoing,
    &.undone {
      color: ${colors.red400};
    }
  }
`;

const CloseIcon = styled(SvgCloseX)`
  position: absolute;
  left: -30px;
  top: 4px;
  width: 20px;
  height: 20px;
  cursor: pointer;
  border-radius: 50%;
  color: ${colors.gray700} !important;
  background: ${colors.gray100} !important;
  box-shadow: rgba(0, 0, 0, 0.22) 0px 4px 10px -1px;
  ${helpers.transition('background-color')}
  &:hover {
    color: ${colors.gray800} !important;
  }
`;

const UndoIcon = styled(SvgUndo)`
  margin-right: 6px;
`;

const RightArrowIcon = styled(SvgRightArrow)`
  margin-left: 4px;
  color: ${colors.brand500};
`;

const UndoneCheckIcon = styled(SvgCheck)`
  margin-left: 6px;
  color: ${colors.red400};
`;

const SavingProgressIcon = styled(SvgProgress)`
  margin-left: 6px;
  color: ${colors.brand500};
`;

const UndoingProgressIcon = styled(SvgProgress)`
  margin-left: 6px;
  color: ${colors.red400};
`;

const UndoButton = styled.button`
 ${helpers.forceInitialize}
 ${helpers.forceCenterFlex}
  width: 74px !important;
  height: 30px !important;
  border-radius: 3px !important;
  cursor: pointer !important;
  font-weight: 500 !important;
  background-color: ${colors.white} !important;
  ${helpers.transition('color', 'background-color')}
  color: ${colors.gray500} !important;
  svg {
    color: ${colors.gray500} !important;
  }
  &:hover {
    color: ${colors.gray700} !important;
    svg {
      color: ${colors.gray700} !important;
    }
  }
`;

const Saved = styled.span`
  display: flex;
  align-items: center;
  font-family: 'system-ui', 'Open Sans', 'Apple SD Gothic Neo', 'Noto Sans KR';
  font-size: 13px;
  cursor: pointer;
  color: ${colors.brand500};
  &:hover {
    color: ${colors.brand700};
    svg {
      color: ${colors.brand700};
    }
  }
`;

function Header({
  saveStatus,
  deleteCard,
  isGuest,
  savedHighlightLength = 0,
  onClickCloseWidgetButton,
  showCloseButton,
  cardEid,
  workspaces,
  currentWorkspaceId,
}) {
  const handleClickUndoButton = useCallback(() => {
    if (saveStatus === 'saving' || saveStatus === 'undoing') return;
    deleteCard();
  }, [saveStatus]);

  const handleClickSaved = useCallback(() => {
    const currentWorkspace = _.find(
      workspaces,
      ({ id }) => id === currentWorkspaceId,
    );
    window.open(
      `https://${currentWorkspace.eid}.${getHostName()}?card=${cardEid}`,
    );
  }, [cardEid, workspaces, currentWorkspaceId]);

  useEffect(() => {
    if (saveStatus === 'undone')
      setTimeout(() => {
        unmountWidget();
        sendMessageToResetWidgetOpenedTabId();
      }, 300);
  }, [saveStatus]);
  return (
    <WidgetHeader>
      {showCloseButton && <CloseIcon onClick={onClickCloseWidgetButton} />}
      <UndoButton onClick={handleClickUndoButton}>
        <UndoIcon />
        Undo
      </UndoButton>
      {!isGuest && (
        <div className={saveStatus}>
          {saveStatus === 'saving' && (
            <>
              Saving <SavingProgressIcon />
            </>
          )}
          {saveStatus === 'saved' && (
            <Saved onClick={handleClickSaved}>
              {savedHighlightLength === 1 ? (
                <>
                  {`${savedHighlightLength} highlight `}
                  saved
                </>
              ) : savedHighlightLength > 1 ? (
                <>
                  {`${savedHighlightLength} highlights `}
                  saved
                </>
              ) : (
                <>Saved to Library</>
              )}
              <RightArrowIcon />
            </Saved>
          )}
          {saveStatus === 'undoing' && (
            <>
              Undoing
              <UndoingProgressIcon />
            </>
          )}
          {saveStatus === 'undone' && (
            <>
              Undone
              <UndoneCheckIcon />
            </>
          )}
        </div>
      )}
    </WidgetHeader>
  );
}

Header.propTypes = {
  saveStatus: PropTypes.oneOf(['saving', 'saved', 'undoing', 'undone', ''])
    .isRequired,
  deleteCard: PropTypes.func.isRequired,
  onClickCloseWidgetButton: PropTypes.func.isRequired,
  showCloseButton: PropTypes.bool.isRequired,
  isGuest: PropTypes.bool.isRequired,
  savedHighlightLength: PropTypes.number,
  cardEid: PropTypes.string,
  workspaces: PropTypes.array,
  currentWorkspaceId: PropTypes.string,
};

const mapStateToProps = state => {
  const {
    widget: {
      isSaving = false,
      isSaved = false,
      isUndoing = false,
      isUndone = false,
      myRole,
      savedHighlightLength,
      blockUserAction,
      workspaces,
      currentWorkspaceId,
    },
    card: { cardEid },
  } = state;
  let saveStatus = '';

  if (isUndoing) {
    saveStatus = 'undoing';
  } else if (isSaving || blockUserAction) {
    saveStatus = 'saving';
  } else if (isSaved) {
    saveStatus = 'saved';
  } else if (isUndone) {
    saveStatus = 'undone';
  }

  return {
    saveStatus: saveStatus,
    isGuest: myRole === Authority.EntireRoles['GUEST'],
    savedHighlightLength,
    cardEid,
    workspaces,
    currentWorkspaceId,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    deleteCard: deleteCard(dispatch),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Header);
