import {
  sendMessageToGetSettings,
  sendMessageToDisableHighlight,
  sendMessageToEnableHighlight,
} from '../messagePassing/sendMessage';
import {
  showEnableHighlightAlert,
  showDisableHighlightAlert,
} from '../highlighter/highlightAlert';
import createPopup from './createPopup';

const rootClassName = 'additor-settings-root';
const backgroundClassName = 'additor-settings-background';

let settingsRoot;
let settingsBackground;

export function hideSettings() {
  if (settingsRoot) {
    settingsRoot.remove();
  }
}

export async function showSettings() {
  const response = await sendMessageToGetSettings();
  const {
    data: { settings: initialSettings },
  } = response;
  settingsRoot = document.createElement('div');
  settingsBackground = document.createElement('div');

  settingsRoot.classList.add(rootClassName);
  settingsBackground.classList.add(backgroundClassName);
  settingsBackground.onclick = hideSettings;

  settingsRoot.appendChild(settingsBackground);

  createPopup({
    root: settingsRoot,
    onClickCancelButton: hideSettings,
    onClickSaveButton: nextSettings => {
      if (initialSettings.highlightable !== nextSettings.highlightable) {
        if (nextSettings.highlightable) {
          sendMessageToEnableHighlight();
          showEnableHighlightAlert();
        } else {
          sendMessageToDisableHighlight();
          showDisableHighlightAlert();
        }
      }
      hideSettings();
    },
    initialSettings,
  });

  document.documentElement.appendChild(settingsRoot);
}
