import React from 'react';
import styled from 'styled-components/macro';
import { showSettings } from 'contentScript/settings';
import colors from 'common/themes/colors';
import helpers from 'common/themes/helpers';

import SvgSettings from './icons/settings.svg';
import SvgTips from './icons/tips.svg';

const FooterWrapper = styled.div`
  width: 100%;
  height: 40px;
  border-radius: 0 0 5px 5px;
  box-shadow: 0 -1px 0 0 ${colors.gray80};
  background-color: ${colors.gray50};
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 26px;

  & > div {
    font-size: 12px;
    font-weight: 500;
    color: ${colors.gray500};
    ${helpers.centerFlex}
  }
`;

const SettingsIcon = styled(SvgSettings)`
  margin-left: 4px;
  cursor: pointer;
  color: ${colors.gray500};
  ${helpers.transition('color')}
`;

const TooltipText = styled.div`
  visibility: hidden;
  position: absolute;
  right: calc(100% + 12px);
  top: -4px;
  width: 130px;
  background-color: #16273a;
  color: ${colors.white};
  text-align: center;
  padding: 5px 0;
  border-radius: 3px;
  z-index: 1;
  opacity: 0;
  transition: opacity 0.6s;
  font-size: 11px;

  &:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 100%;
    margin-top: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: transparent transparent transparent #16273a;
  }
`;

const SettingsIconWrapper = styled.div`
  display: flex;
  cursor: pointer;
  position: relative;

  &:hover {
    ${TooltipText} {
      visibility: visible;
      opacity: 1;
    }
  }
`;

const TipsIconWrapper = styled.a`
  display: flex;
  cursor: pointer;
  position: relative;

  &:hover {
    ${TooltipText} {
      visibility: visible;
      opacity: 1;
    }
  }
`;

const TipsIcon = styled(SvgTips)`
  color: ${colors.gray500};

  &:hover {
    color: ${colors.gray700};
  }
`;

const Divider = styled.div`
  width: 1px;
  height: 15px;
  background-color: ${colors.gray300};
  margin: 0 6px;
`;

function Footer() {
  return (
    <FooterWrapper>
      <SettingsIconWrapper onClick={showSettings}>
        <SettingsIcon />
        <TooltipText>Extension Settings</TooltipText>
      </SettingsIconWrapper>
      <Divider />
      <TipsIconWrapper
        href="https://additor-support.additor.io/page/WWbretVjx7yQ"
        target="_blank"
        rel="noreferrer noopener"
      >
        <TipsIcon />
        <TooltipText>Extension Guide</TooltipText>
      </TipsIconWrapper>
    </FooterWrapper>
  );
}

Footer.propTypes = {};

export default Footer;
