import _ from 'lodash';
import { cardDefaultThumbSrc } from 'common/constants';
import actionTypes from '../action/actionTypes';

const initialState = {
  cardId: '',
  cardEid: '',
  cardTitle: '',
  cardThumbnails: [],
  cardUri: '',
  cardDescription: '',
  cardTags: [],
};

export default function card(state = initialState, action = {}) {
  const actions = {
    [actionTypes.CREATE_CARD]() {
      return state;
    },
    [actionTypes.CREATE_CARD_FAIL]() {
      return {
        ...state,
      };
    },
    [actionTypes.CREATE_CARD_SUCCESS]() {
      const createdCard = _.get(action, 'response.data.createdCard');
      const firstThumbnail = _.get(
        createdCard,
        'images.thumb',
        cardDefaultThumbSrc,
      );
      const thumbnails = _.compact(
        _.union([firstThumbnail], state.cardThumbnails),
      );
      if (thumbnails.length === 0) {
        thumbnails.push(cardDefaultThumbSrc);
      }
      return {
        ...state,
        cardId: createdCard.id,
        cardEid: createdCard.eid,
        cardTitle: createdCard.name,
        cardThumbnails: thumbnails,
        cardDescription: createdCard.desc,
      };
    },
    [actionTypes.DELETE_CARD]() {
      return state;
    },
    [actionTypes.DELETE_CARD_SUCCESS]() {
      return initialState;
    },
    [actionTypes.UPDATE_CARD]() {
      return state;
    },
    [actionTypes.UPDATE_CARD_SUCCESS]() {
      const { data } = action.response;
      if (data.updatedTitle) {
        return {
          ...state,
          cardTitle: data.updatedTitle,
        };
      }
      if (data.updatedTags) {
        return {
          ...state,
          cardTags: data.updatedTags,
        };
      }
      return state;
    },
  };

  if (typeof actions[action.type] !== 'function') {
    return state;
  }

  try {
    return actions[action.type]();
  } catch (e) {
    return state;
  }
}
