import _ from 'lodash';
import actionTypes from '../action/actionTypes';

const initialState = {
  user: null,
  isSaving: false,
  isSaved: false,
  isUndoing: false,
  isUndone: false,
  isHidden: false,
  workspaces: [],
  folders: [],
  recentFolderId: '',
  currentWorkspaceId: '',
  myRole: 0,
  savedHighlightLength: 0,
  disableWorkspace: false,
  signedOutWorkspace: false,
  error: false,
  blockUserAction: false,
};

export default function widget(state = initialState, action = {}) {
  const actions = {
    [actionTypes.SAVING]() {
      return {
        ...state,
        isSaving: true,
      };
    },
    [actionTypes.SAVED]() {
      return {
        ...state,
        isSaving: false,
        isSaved: true,
      };
    },
    [actionTypes.CREATE_CARD]() {
      return {
        ...state,
        isSaving: true,
      };
    },
    [actionTypes.CREATE_CARD_FAIL]() {
      return {
        ...state,
      };
    },
    [actionTypes.CREATE_CARD_SUCCESS]() {
      return {
        ...state,
        isSaving: false,
        isSaved: true,
      };
    },
    [actionTypes.DELETE_CARD]() {
      return {
        ...state,
        isSaved: false,
        isUndoing: true,
      };
    },
    [actionTypes.DELETE_CARD_SUCCESS]() {
      return {
        ...state,
        isUndoing: false,
        isUndone: true,
      };
    },
    [actionTypes.SWITCH_WORKSPACE]() {
      return {
        ...state,
        blockUserAction: true,
      };
    },
    [actionTypes.SWITCH_WORKSPACE_FAIL]() {
      return {
        ...state,
        currentWorkspaceId: action.workspaceId,
        disableWorkspace: true,
        blockUserAction: false,
      };
    },
    [actionTypes.SWITCH_WORKSPACE_SUCCESS]() {
      const newState = _.get(action, 'response.data.initialStates.widget', {});
      return {
        ...state,
        ...newState,
        currentWorkspaceId: action.id,
        disableWorkspace: false,
        signedOutWorkspace: false,
        blockUserAction: false,
      };
    },
    [actionTypes.HIDE_WIDGET]() {
      return {
        ...state,
        isHidden: true,
      };
    },
    [actionTypes.SHOW_WIDGET]() {
      return {
        ...state,
        isHidden: false,
      };
    },
    [actionTypes.UPDATE_INITIAL_STATES]() {
      const nextStates = action.states;
      return {
        ...state,
        ...nextStates.widget,
        disableWorkspace: false,
        signedOutWorkspace: false,
      };
    },
    [actionTypes.UPDATE_WORKSPACES]() {
      const { currentWorkspaceId } = state;
      const newWorkspaces = action.workspaces;
      const currentWorkspace = _.find(newWorkspaces, {
        id: currentWorkspaceId,
      });
      if (currentWorkspace) {
        return {
          ...state,
          workspaces: action.workspaces,
          signedOutWorkspace: false,
        };
      } else {
        return {
          ...state,
          signedOutWorkspace: true,
        };
      }
    },
    [actionTypes.CREATE_HIGHLIGHT_SUCCESS]() {
      return {
        ...state,
        savedHighlightLength: action.highlightsLength,
      };
    },
    [actionTypes.DELETE_HIGHLIGHT_SUCCESS]() {
      return {
        ...state,
        savedHighlightLength: action.highlightsLength,
      };
    },
    [actionTypes.DISABLE_WORKSPACE]() {
      return {
        ...state,
        disableWorkspace: true,
      };
    },
    [actionTypes.SHOW_ERROR_VIEW]() {
      return {
        ...state,
        error: true,
      };
    },
    [actionTypes.UPDATE_CARD]() {
      return {
        ...state,
        isSaving: true,
        isSaved: false,
      };
    },
    [actionTypes.UPDATE_CARD_SUCCESS]() {
      return {
        ...state,
        isSaving: false,
        isSaved: true,
      };
    },
  };

  if (typeof actions[action.type] !== 'function') {
    return state;
  }

  try {
    return actions[action.type]();
  } catch (e) {
    return state;
  }
}
